        <div class="verticalData">
          <div class="verticalDataBox">
            <div class="left">
              <img :src="`${chuli(item.albumInfo.cover)}`" alt="" />
            </div>
            <div class="right">
              <div>
                <div class="Rtitle">{{ item.albumInfo.title }}</div>
                <div class="salePoint">{{ item.albumInfo.salePoint }}</div>
              </div>
              <div class="bottom">
                <div class="statCountInfo">
                  <div class="play">
                    <svg
                      t="1665023374138"
                      class="icon"
                      viewBox="0 0 1024 1024"
                      version="1.1"
                      xmlns="http://www.w3.org/2000/svg"
                      p-id="2376"
                      width="48"
                      height="48"
                    >
                      <path
                        d="M224 938.713333a53.58 53.58 0 0 1-53.333333-53.433333V138.72a53.333333 53.333333 0 0 1 80.886666-45.666667l618.666667 373.28a53.333333 53.333333 0 0 1 0 91.333334l-618.666667 373.28a53.16 53.16 0 0 1-27.553333 7.766666z m0.046667-810.666666a10.98 10.98 0 0 0-5.333334 1.42 10.466667 10.466667 0 0 0-5.38 9.253333v746.56a10.666667 10.666667 0 0 0 16.18 9.133333l618.666667-373.28a10.666667 10.666667 0 0 0 0-18.266666l-618.666667-373.28a10.386667 10.386667 0 0 0-5.446666-1.586667z"
                        fill="#999999"
                        p-id="2377"
                      ></path>
                    </svg>
                    {{
                      item.statCountInfo.playCount > 100000000
                        ? (item.statCountInfo.playCount / 100000000).toFixed(2) + "亿"
                        : item.statCountInfo.playCount > 10000
                        ? (item.statCountInfo.playCount / 10000).toFixed(2) + "万"
                        : item.statCountInfo.playCount
                    }}
                  </div>
                  <div class="diso">
                    <svg
                      t="1664763290338"
                      class="icon"
                      viewBox="0 0 1024 1024"
                      version="1.1"
                      xmlns="http://www.w3.org/2000/svg"
                      p-id="14843"
                      width="48"
                      height="48"
                    >
                      <path
                        d="M64 640a21.333333 21.333333 0 0 1-21.333333-21.333333v-213.333334a21.333333 21.333333 0 0 1 42.666666 0v213.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM192 810.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-554.666666a21.333333 21.333333 0 0 1 42.666666 0v554.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM320 981.333333a21.333333 21.333333 0 0 1-21.333333-21.333333v-896a21.333333 21.333333 0 0 1 42.666666 0v896a21.333333 21.333333 0 0 1-21.333333 21.333333zM448 938.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-810.666666a21.333333 21.333333 0 0 1 42.666666 0v810.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM576 768a21.333333 21.333333 0 0 1-21.333333-21.333333v-469.333334a21.333333 21.333333 0 0 1 42.666666 0v469.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM704 938.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-810.666666a21.333333 21.333333 0 0 1 42.666666 0v810.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM832 768a21.333333 21.333333 0 0 1-21.333333-21.333333v-469.333334a21.333333 21.333333 0 0 1 42.666666 0v469.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM960 640a21.333333 21.333333 0 0 1-21.333333-21.333333v-213.333334a21.333333 21.333333 0 0 1 42.666666 0v213.333334a21.333333 21.333333 0 0 1-21.333333 21.333333z"
                        fill="#73737b"
                        p-id="14844"
                      ></path>
                    </svg>
                    {{ item.statCountInfo.trackCount }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["album", "loading"],
  methods: {
    chuli: function (res) {
      if (res?.startsWith("http")) {
        return res;
      } else if (res?.startsWith("http") == false) {
        return `https://imagev2.xmcdn.com${
          res[0] === "/" ? res : "/" + res
        }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
      } else {
        return "1.png";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.tabCenter {
  .tabCenterBox {
    > .title {
      margin: 15rem 0 0 0;
      padding: 10rem;
      font-size: 18rem;
      font-weight: bold;
    }
    .verBox {
      .verticalData {
        .verticalDataBox {
          padding: 15rem;
          // margin: 0rem 10rem;
          display: flex;
          border-bottom: 1rem solid #cccccc2e;
          &:not(:last-child) {
            border-bottom: 1rem solid #cccccc4a;
          }
          .left {
            display: flex;
            flex-shrink: 0;
            img {
              width: 70rem;
              height: 70rem;
              border-radius: 5rem;
            }
          }
          .right {
            width: calc(100% - 95rem);
            margin-left: 20rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .Rtitle {
              font-size: 14rem;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
            }
            .salePoint {
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              color: #a6a6a6;
              font-size: 12rem;
              margin: 5rem 0;
            }
            .bottom {
              display: flex;
              align-items: center;
              .category {
                display: flex;
                align-items: center;
                svg {
                  width: 14rem;
                  height: 14rem;
                  margin-right: 2rem;
                }
                span {
                  font-size: 12rem;
                  color: #a6a6a6;
                }
              }
              .statCountInfo {
                display: flex;
                align-items: center;
                .diso {
                  font-size: 12rem;
                  display: flex;
                  align-items: center;
                  color: #a6a6a6;
                  margin-left: 5rem;

                  svg {
                    width: 15rem;
                    height: 15rem;
                    margin-right: 7rem;
                  }
                }
                .play {
                  display: flex;
                  align-items: center;
                  margin-right: 10rem;
                  color: #a6a6a6;
                  font-size: 12rem;
                  svg {
                    width: 15rem;
                    height: 15rem;
                    margin-right: 7rem;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
