<template>
  <!-- van-multi-ellipsis--l2 -->
  <ul>
    <div :class="{ vipShow: Soundstate }" class="vip">抱歉<br />当前歌曲需要vip</div>
    <li class="item" v-for="item in itemArr" :key="item.id">
      <div clsss="item-svg">
        <!-- 暂停 -->
        <svg
          v-if="soundSrc.id == item.id && play"
          t="1665062857682"
          @click="changePlay(false)"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="23984"
          width="48"
          height="48"
        >
          <path
            d="M442.8 684c8.1-8.1 13.2-19.4 13.2-31.7V371.8c0-24.7-20.2-45-45-45-12.4 0-23.6 5.1-31.7 13.2s-13.2 19.4-13.2 31.7v280.6c0 24.7 20.2 45 45 45 12.1-0.1 23.4-5.1 31.7-13.3zM644.8 684c8.1-8.1 13.2-19.4 13.2-31.7V371.8c0-24.7-20.2-45-45-45-12.4 0-23.6 5.1-31.7 13.2-8.1 8.1-13.2 19.4-13.2 31.7v280.6c0 24.7 20.2 45 45 45 12.1-0.1 23.4-5.1 31.7-13.3z"
            fill="#999999"
            p-id="23985"
          ></path>
          <path
            d="M512 136.3c50.7 0 99.9 9.9 146.2 29.5 44.7 18.9 84.9 46 119.5 80.6 34.5 34.5 61.6 74.7 80.6 119.5 19.6 46.3 29.5 95.5 29.5 146.2s-9.9 99.9-29.5 146.2c-18.9 44.7-46 84.9-80.6 119.5-34.5 34.5-74.7 61.6-119.5 80.6-46.3 19.6-95.5 29.5-146.2 29.5s-99.9-9.9-146.2-29.5c-44.7-18.9-84.9-46-119.4-80.6-34.5-34.5-61.6-74.7-80.6-119.5-19.6-46.3-29.5-95.5-29.5-146.2s9.9-99.9 29.5-146.2c18.9-44.7 46-84.9 80.6-119.5 34.5-34.5 74.7-61.6 119.4-80.6 46.3-19.6 95.5-29.5 146.2-29.5m0-70C265.9 66.3 66.3 265.9 66.3 512S265.9 957.7 512 957.7 957.7 758.1 957.7 512 758.1 66.3 512 66.3z"
            fill="#999999"
            p-id="23986"
          ></path>
        </svg>
        <!-- 播放 -->
        <svg
          v-else
          @click="sendSoundSrc(item.id)"
          t="1664980849933"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="15697"
          width="48"
          height="48"
        >
          <path
            d="M512 960.08A448.18 448.18 0 0 1 337.58 99.15a448.18 448.18 0 0 1 348.84 825.7A445.17 445.17 0 0 1 512 960.08z m0-832c-211.7 0-383.92 172.22-383.92 383.92S300.3 895.92 512 895.92 895.92 723.7 895.92 512 723.7 128.08 512 128.08z m161.72 357.53L472.46 347.66c-21.23-14.56-50.09 0.65-50.09 26.39V650c0 25.74 28.86 40.95 50.09 26.39l201.26-138a32 32 0 0 0 0-52.78z"
            fill="#999999"
            p-id="15698"
          ></path>
        </svg>
      </div>
      <div
        class="item-cont"
        :class="{ itemcolorShow: soundSrc.id == item.id && play }"
        style="width: 100%"
        @click="
          $router.push({ path: `/sound/${item.id}` });
          nowPlayMusic(item);
          sendSoundSrc(item.id, false);
        "
      >
        <span>{{ item.trackInfo.title }}</span>
      </div>
    </li>
    <div
      class="open"
      @click="itemArrchange()"
      v-show="!itemArrState && itemArr.length > 9"
    >
      加载更多
      <svg
        t="1665031298120"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="12806"
        width="48"
        height="48"
      >
        <path
          d="M500.8 604.779L267.307 371.392l-45.227 45.27 278.741 278.613L779.307 416.66l-45.248-45.248z"
          p-id="12807"
          fill="#f86442"
        ></path>
      </svg>
    </div>
  </ul>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      itemArrState: false,
      itemArr: null,
      Soundstate: false,
    };
  },
  computed: {
    ...mapState(["soundList", "audioplay", "play", "soundSrc"]),
  },
  // props: ["albumList"],
  created() {
    if (this.soundList) {
      this.itemArr = [...this.soundList.trackDetailInfos].splice(0, 10);
    }
  },

  methods: {
    ...mapMutations([
      "changeSoundSrc",
      "SoundList",
      "changePlay",
      "emptyPlaylist",
      "nowPlayMusic",
    ]),
    itemArrchange: function () {
      this.itemArrState = true;
      if (this.soundList.trackDetailInfos) {
        [...this.soundList.trackDetailInfos]
          .splice(10, 20)
          .map((o) => this.itemArr.push(o));
      }
    },
    sendSoundSrc: function (id, state = true) {
      this.axios
        .get(`https://apis.netstart.cn/ximalaya/track/queryTrackPage/${id}`)
        .then((res) => {
          this.changeSoundSrc(res.data.data);
          this.emptyPlaylist(this.itemArr ? this.itemArr : []);
          if (state) {
            setTimeout(() => {
              // console.log(this.audioplay.src);
              if (
                this.audioplay.src === `http://${location.host}/` ||
                this.audioplay.src === ""
              ) {
                //控制vip弹窗，并且1秒后结束
                this.Soundstate = true;
                setTimeout(() => {
                  this.Soundstate = false;
                  this.changeMusic = false;
                }, 1000);
              } else {
                //否则就播放
                this.changePlay(true);
              }
            }, 0);
          }
        });
    },
  },
  watch: {
    soundList: function (res) {
      this.itemArr = [...res.trackDetailInfos].splice(0, 10);
    },
  },
};
</script>
<style lang="less" scoped>
.vip {
  position: fixed;
  text-align: center;
  display: flex;
  align-items: center;
  font-size: 15rem;
  justify-content: center;
  z-index: -1;
  // line-height: 100rem;
  color: #fff;
  top: 75%;
  opacity: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200rem;
  height: 100rem;
  border-radius: 10rem;
  transition: all 0.3s;
  background-color: rgba(51, 51, 51, 0.798);
  &.vipShow {
    top: 50%;
    z-index: 1;
    opacity: 1;
    z-index: 99;
  }
}
.item {
  padding: 10rem 0;
  display: flex;
  align-items: stretch;
  justify-content: center;
  border-bottom: 1rem solid rgba(153, 153, 153, 0.12);
  svg {
    width: 30rem;
    height: 30rem;
  }
  > .item-cont {
    width: 100%;
    overflow: hidden;
    // background-color: #f86442;
    &.itemcolorShow {
      color: #f86442;
    }
  }
  span {
    font-size: 16rem;
    padding: 0 10rem;
    line-height: 26px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}
.open {
  font-size: 15rem;
  font-weight: bold;
  color: #f86442;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 17rem 0;
  svg {
    width: 20rem;
    height: 20rem;
  }
}
</style>
