<script>
import { searchEnd, SearchFilterData } from "../../apis";
import FooterContent from "../../components/footerContent.vue";
let num = 1;
export default {
  components: {
    FooterContent,
  },
  data() {
    return {
      loading: false,
      finished: false,
      error: false,
      searchZhubo: [],
      showLoading: true,
      tabhide: true,
      zhubo: "",
      zhuboValue: "相关度",
    };
  },
  watch: {
    searchZhubo() {
      this.showLoading = false;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrolling);
  },
  methods: {
    songTimes(time) {
      let min = Math.floor(time / 60);
      let sed = Math.floor(time % 60);
      return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
    },
    scrolling() {
      try {
        // console.log(document.body.scrollTop, window.pageYOffset);
        if (window.pageYOffset === 0) {
          // console.log("到头了");
          this.tabhide = true;
        } else {
          this.tabhide = false;
        }
      } catch (error) {
        error;
      }
    },
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
    numAdd: function () {
      num++;
    },
    onLoad() {
      searchEnd({ kw: this.$route.params.id, core: "user", page: num })
        .then((res) => {
          this.numAdd();
          let arr = res.data.data.userViews.users;
          // console.log(this.searchZhubo, arr);
          this.searchZhubo = new Set([...this.searchZhubo, ...arr]);
          this.loading = false;
          if (this.searchZhubo.length <= 40) {
            this.finished = true;
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },
    sendData: function ({ kw, page, category_id, condition } = {}) {
      SearchFilterData({
        kw,
        core: "user",
        page,
        category_id,
        condition,
      }).then((o) => {
        this.searchZhubo = o.data.data.userViews.users;
      });
    },
    changeCondition(state, name) {
      this.zhuboValue = name;
      this.zhubo = state;
      this.sendData({ kw: this.$route.params.id, core: "user", condition: this.zhubo });
    },
  },
};
</script>

