        <li
          :class="{ active: defaultPlaybackRate == 0.5 }"
          @click="changeAudioSpeed(0.5)"
        >
          0.5
        </li>
        <li :class="{ active: defaultPlaybackRate == 1 }" @click="changeAudioSpeed(1)">
          1
        </li>
        <li
          :class="{ active: defaultPlaybackRate == 1.5 }"
          @click="changeAudioSpeed(1.5)"
        >
          1.5
        </li>
        <li :class="{ active: defaultPlaybackRate == 2 }" @click="changeAudioSpeed(2)">
          2
        </li>
        <li :class="{ active: defaultPlaybackRate == 3 }" @click="changeAudioSpeed(3)">
