</template>

<script>
import AlbumItemSound from "@/components/AlbumItemSound.vue";
import SimilarAlbums from "@/components/SimilarAlbums.vue";
import CommentRegion from "@/components/commentRegion.vue";
import ClassicAlbums from "@/components/ClassicAlbums.vue";
import HeaderOpen from "@/components/headerOpen.vue";
import Footer from "@/components/footerContent.vue";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    AlbumItemSound,
    SimilarAlbums,
    CommentRegion,
    ClassicAlbums,
    Footer,
    HeaderOpen,
  },
  computed: {
    ...mapState([
      "soundList",
      "audioplay",
      "play",
      "timeupdate",
      "durationchange",
      "playList",
      "defaultPlaybackRate",
    ]),
  },
  //路由改变前
  beforeRouteUpdate(to, from, next) {
    // console.log("当前路由切换");
    // this.changeMusic(false);
    this.toggleAlbum = 1;
    this.showLoading = true;

    let ass = this.axios.get(
      `https://apis.netstart.cn/ximalaya/track/queryTrackPage/${to.params.id}`
    );

    // 评论
    let first = this.axios.get(
      "https://apis.netstart.cn/ximalaya/track/queryTrackCommentsFirstPage",
      {
        params: {
          trackId: to.params.id,
          page: 1,
          pageSize: 10,
        },
      }
    );

    //经典必听
    let sed = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryClassicListenAlbums"
    );

    let four = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage",
      {
        params: {
          albumId: this.soundList.id,
          page: 1,
          pageSize: 20,
          asc: true,
          countKeys: "play,comment",
        },
      }
    );
    let allAxiosAjax = Promise.all([first, sed, ass, four]);

    allAxiosAjax.then((res) => {
      this.comment = res[0].data.data;

      this.classicAlbums = res[1].data.data;

      this.changeSoundSrc(res[2].data.data);

      this.soundCount = res[2].data.data;
      this.nowPlayMusic(res[2].data.data);
      this.zhubo = res[2].data.data.albumDetailInfo.albumInfo.anchorId;

      this.SoundList(res[3].data.data);
      this.emptyPlaylist(res[3].data.data.trackDetailInfos);
      if (res) {
        //主播信息
        let first = this.axios.get(
          `https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${res[2].data.data.albumDetailInfo.albumInfo.anchorId}`
        );
        //主播专辑
        let sed = this.axios.get(
          "https://apis.netstart.cn/ximalaya/anchor/queryAnchorAlbumsByPage",
          {
            params: {
              anchorId: res[2].data.data.albumDetailInfo.albumInfo.anchorId,
              page: 1,
              pageSize: 3,
              asc: false,
            },
          }
        );
        //相关专辑 ~ 相关推荐
        let three = this.axios.get(
          "https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums",
          {
            params: {
              albumId: res[2].data.data.trackDetailInfo.trackInfo.albumId,
              page: 1,
              pageSize: 6,
            },
          }
        );
        let four = this.axios.get(
          "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage",
          {
            params: {
              albumId: this.soundList.id,
              page: 1,
              pageSize: 20,
              asc: true,
              countKeys: "play,comment",
            },
          }
        );
        let allAjax = Promise.all([first, sed, three, four]);
        allAjax.then((res) => {
          this.albumAuthor = res[0].data.data;

          this.AlbumsByPage = res[1].data.data;

          this.albumBrief = res[2].data.data.albumBriefDetailInfos;
          this.SoundList(res[3].data.data);
          if (this.playList) {
            let index = this.playList.findIndex((o) => {
              return o.id == Number(this.$route.params.id);
            });
            //控制上一首与下一首
            if (index === 0) {
              this.pevrTrue = true;
            } else {
              this.pevrid = this.playList[index - 1].id;
              this.pevrTrue = false;
            }
            if (index === this.playList.length - 1) {
              this.nextTrue = true;
            } else {
              this.nextid = this.playList[index + 1].id;
              this.nextTrue = false;
            }
          }
        });
      }

      setTimeout(() => {
        this.showLoading = false;
        // console.log("这里是路由创建前", this.showLoading);
      }, 0);
    });
    next();
  },

  created() {
    this.toggleAlbum = 1;
    this.showLoading = true;
    // console.log(this.showLoading);
    let ass = this.axios.get(
      `https://apis.netstart.cn/ximalaya/track/queryTrackPage/${this.$route.params.id}`
    );

    // 评论
    let first = this.axios.get(
      "https://apis.netstart.cn/ximalaya/track/queryTrackCommentsFirstPage",
      {
        params: {
          trackId: this.$route.params.id,
          page: 1,
          pageSize: 10,
        },
      }
    );

    //经典必听
    let sed = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryClassicListenAlbums"
    );
    //音轨列表

    let allAxiosAjax = Promise.all([first, sed, ass]);

    allAxiosAjax.then((res) => {
      this.comment = res[0].data.data;
      this.classicAlbums = res[1].data.data;
      this.changeSoundSrc(res[2].data.data);
      //音轨内容
      this.soundCount = res[2].data.data;
      this.nowPlayMusic(res[2].data.data);
      //主播id
      this.zhubo = res[2].data.data.albumDetailInfo.albumInfo.anchorId;

      if (res) {
        //主播信息
        let first = this.axios.get(
          `https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${res[2].data.data.albumDetailInfo.albumInfo.anchorId}`
        );
        let four = this.axios.get(
          "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage",
          {
            params: {
              albumId: res[2].data.data.trackDetailInfo.trackInfo.albumId,
              page: 1,
              pageSize: 20,
              asc: true,
              countKeys: "play,comment",
            },
          }
        );
        //主播专辑
        let sed = this.axios.get(
          "https://apis.netstart.cn/ximalaya/anchor/queryAnchorAlbumsByPage",
          {
            params: {
              anchorId: res[2].data.data.albumDetailInfo.albumInfo.anchorId,
              page: 1,
              pageSize: 3,
              asc: false,
            },
          }
        );
        //相关专辑 ~ 相关推荐
        let three = this.axios.get(
          "https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums",
          {
            params: {
              albumId: res[2].data.data.trackDetailInfo.trackInfo.albumId,
              page: 1,
              pageSize: 6,
            },
          }
        );

        let allAjax = Promise.all([first, sed, three, four]);
        allAjax.then((res) => {
          this.albumAuthor = res[0].data.data;
          this.AlbumsByPage = res[1].data.data;
          this.albumBrief = res[2].data.data.albumBriefDetailInfos;
          this.emptyPlaylist(res[3].data.data.trackDetailInfos);
          this.SoundList(res[3].data.data);
          //计算下一首歌曲的id是什么
          if (this.playList) {
            let index = this.playList.findIndex((o) => {
              return o.id == Number(this.$route.params.id);
            });
            if (index === 0) {
              this.pevrTrue = true;
            } else {
              this.pevrid = this.playList[index - 1]?.id;
              this.pevrTrue = false;
            }
            if (index === this.playList.length - 1) {
              this.nextTrue = true;
            } else {
              this.nextid = this.playList[index + 1].id;
              this.nextTrue = false;
            }
          }
        });
      }

      setTimeout(() => {
        this.showLoading = false;
        // console.log("这里是路由创建前", this.showLoading);
      }, 0);
    });
  },

  data: function () {
    return {
      soundCount: null,
      albumAuthor: null,
      AlbumsByPage: null,
      comment: null,
      albumBrief: null,
      classicAlbums: null,
      pseudoTime: null,
      openView: false,
      ctrlPage: false,
      showLoading: true,
      openEdit: false,
      toggleAlbum: 1,
      Soundstate: false,
      nextid: 0,
      pevrid: 0,
      nextTrue: false,
      pevrTrue: false,
      zhubo: 0,
      scArr: JSON.parse(localStorage.getItem("scArr")) || [],
      zbArr: JSON.parse(localStorage.getItem("zbArr")) || [],
    };
  },
  methods: {
    ...mapMutations([
      "changeSoundSrc",
      "SoundList",
      "changePlay",
      "updateTime",
      "changeMusicTime",
      "nowPlayMusic",
      "emptyPlaylist",
      "changeMusic",
      "changeAudioSpeed",
    ]),
    //控制显示的模板
    showCtrlSpeedPage(status) {
      this.ctrlPage = status;
    },
    clickToggleAlbum: function () {
      this.toggleAlbum += 1;
      console.log(this.toggleAlbum);
      this.axios
        .get("https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums", {
          params: {
            albumId: this.soundList.id,
            page: this.toggleAlbum,
            pageSize: 6,
          },
        })
        .then((res) => {
          this.albumBrief = res.data.data.albumBriefDetailInfos;
        });
    },
    songTimes(time) {
      let min = Math.floor(time / 60);
      let hou = min > 60 ? Math.floor(min / 60) : min;
      let sed = Math.floor(time % 60);
      if (min > 60) {
        return `${hou < 10 ? `0${hou}` : hou}:${
          min < 10 ? `0${min - 60 * hou}` : min - 60 * hou
        }:${sed < 10 ? `0${sed}` : sed}`;
      } else {
        return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
      }
    },

    sendSound: function (state = true) {
      // console.log(this.audioplay.src);
      if (
        this.audioplay.src === `http://${location.host}/` ||
        this.audioplay.src === ""
      ) {
        this.Soundstate = true;
        // console.log("当前src为空");
        setTimeout(() => {
          this.Soundstate = false;
          this.changeMusic = false;
        }, 1000);
      } else {
        // console.log(state);
        if (state) {
          this.changePlay(state);
        } else {
          this.changePlay(state);
        }
      }
    },
    Sc(scId) {
      if (!this.scArr.length) {
        return true;
      } else {
        if (this.scArr.includes(scId)) {
          return false;
        } else {
          return true;
        }
      }
    },
    clickSc(scId) {
      if (!this.scArr.length) {
        this.scArr = [scId, ...this.scArr];
        localStorage.setItem("scArr", JSON.stringify(this.scArr));
        return true;
      } else {
        if (this.scArr.includes(scId)) {
          this.scArr = this.scArr.filter((o) => o !== scId);
          localStorage.setItem("scArr", JSON.stringify(this.scArr));
          return false;
        } else {
          this.scArr = [scId, ...this.scArr];
          localStorage.setItem("scArr", JSON.stringify(this.scArr));
          return true;
        }
      }
    },
    zb(zbId) {
      if (!this.zbArr.length) {
        return true;
      } else {
        if (this.zbArr.includes(zbId)) {
          return false;
        } else {
          return true;
        }
      }
    },
  },
  watch: {
    timeupdate: function () {
      if (!this.openEdit) {
        this.pseudoTime = this.timeupdate;
      }
    },
  },
};
</script>
