      <section class="classic">
        <div class="title">
          <span>经典必听</span>
        </div>
        <div class="classicNav"></div>
        <ClassicAlbums :classicAlbums="classicAlbums" />
      </section>
    </div>

    <Footer v-show="albumContent && albumAuthor" />

    <section
      class="buy"
      v-if="buyAlbums?.singleBuyPriceInfo && buyAlbums?.vipAlbumGuideInfo"
    >
      <div class="buyBox">
        <div class="left">
          购买已更全集 <span>￥{{ buyAlbums?.singleBuyPriceInfo?.buyPrice }}</span>
        </div>
        <div class="right">
          {{ buyAlbums?.vipAlbumGuideInfo?.vipBtnText }}
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import AlbumItem from "@/components/AlbumItem.vue";
import SimilarAlbums from "@/components/SimilarAlbums.vue";
import ClassicAlbums from "@/components/ClassicAlbums.vue";
import HeaderOpen from "@/components/headerOpen.vue";
import Footer from "@/components/footerContent.vue";
import { mapState } from "vuex";
import { mapMutations } from "vuex";

// console.log(vant);
export default {
  //当我点击播放时，立即向vuex发送数据，当vuex接受到数据后就会播放当前音频
  // 当我销毁路由前，向vuex发送一个状态 ，使下面的播放栏出来 ，需要使用路由销毁前的生命周期
  //材料为   带过来的id  需要播放的音频地址  判断前面到底有没有在播放音乐需不需要隐藏播放栏
  components: {
    AlbumItem,
    SimilarAlbums,
    ClassicAlbums,
    Footer,
    HeaderOpen,
  },
  computed: {
    ...mapState(["footerState", "soundList"]),
  },
  beforeRouteLeave(to, from, next) {
    console.log("离开当前路由");
    this.changeMusic(false);
    next();
  },
  created() {
    this.toggleAlbum = 1;
    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/album/queryAlbumPage/${this.$route.params.id}`
      )
      .then((res) => {
        if (res) {
          this.axios
            .get(
              `https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${res.data.data.anchorId}`
            )
            .then((o) => {
              this.albumContent = res.data.data;
              this.albumAuthor = o.data.data;
            });
          this.axios
            .get("https://apis.netstart.cn/ximalaya/anchor/queryAnchorAlbumsByPage", {
              params: {
                anchorId: res.data.data.anchorId,
                page: 1,
                pageSize: 3,
                asc: false,
              },
            })
            .then((res) => {
              this.AlbumsByPage = res.data.data;
              if (res) {
                try {
                  this.$refs.albumPresent.style.height =
                    this.$refs.albumPresentcontent.offsetHeight + "px";
                } catch (error) {
                  error;
                }
              }
            });
        }
      });
    //音轨列表
    let list = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage?",
      {
        params: {
          albumId: this.$route.params.id,
          pageSize: 20,
          asc: true,
          countKeys: "play,comment",
        },
      }
    );

    let sed = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums",
      {
        params: {
          albumId: this.$route.params.id,
          page: 1,
          pageSize: 6,
        },
      }
    );

    let three = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryClassicListenAlbums"
    );

    let four = this.axios.get(
      `https://m.ximalaya.com/m-revision/page/album/queryAlbumPageActivityInfo/${this.$route.params.id}`,
      {
        params: {
          v: Date.now(),
        },
      }
    );

    let allAxiosAjax = Promise.all([list, sed, three, four]);
    allAxiosAjax.then((res) => {
      this.albumList = res[0].data.data;
      this.SoundList(res[0].data.data);
      console.log("album已经进入", res[0].data.data);

      // console.log("这7里是更新前", this.albumList, res[0].data.data);

      this.albumBrief = res[1].data.data.albumBriefDetailInfos;

      this.classicAlbums = res[2].data.data;

      this.buyAlbums = res[3].data.data;

      this.showLoading = false;
    });
  },
  // mounted(){
  beforeRouteUpdate(to, from, next) {
    this.toggleAlbum = 1;
    this.tomatoesShow = false;
    this.openView = false;
    this.killHeight = true;
    this.axios
      .get(`https://apis.netstart.cn/ximalaya/album/queryAlbumPage/${to.params.id}`)
      .then((res) => {
        if (res) {
          this.axios
            .get(
              `https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/${res.data.data.anchorId}`
            )
            .then((o) => {
              this.albumContent = res.data.data;
              this.albumAuthor = o.data.data;
            });
          this.axios
            .get("https://apis.netstart.cn/ximalaya/anchor/queryAnchorAlbumsByPage", {
              params: {
                anchorId: res.data.data.anchorId,
                page: 1,
                pageSize: 3,
                asc: false,
              },
            })
            .then((res) => {
              this.AlbumsByPage = res.data.data;
              // if (res) {
              //   setTimeout(() => {
              //     this.$refs.albumPresent.style.height =
              //       this.$refs.albumPresentcontent.offsetHeight + "px";
              //   }, 100);
              // }
            });
        }
      });
    //音轨列表
    // https: apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage?albumId=16411402&page=1&pageSize=10&asc=true&countKeys=play,comment
    let list = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage",
      {
        params: {
          albumId: to.params.id,
          pageSize: 10,
          asc: true,
          countKeys: "play,comment",
        },
      }
    );
    let sed = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums",
      {
        params: {
          albumId: to.params.id,
          page: 1,
          pageSize: 6,
        },
      }
    );

    let three = this.axios.get(
      "https://apis.netstart.cn/ximalaya/album/queryClassicListenAlbums"
    );

    let four = this.axios.get(
      `https://m.ximalaya.com/m-revision/page/album/queryAlbumPageActivityInfo/${to.params.id}`,
      {
        params: {
          v: Date.now(),
        },
      }
    );

    let allAxiosAjax = Promise.all([list, sed, three, four]);
    allAxiosAjax.then((res) => {
      //音轨列表
      console.log(res[0].data.data);
      this.albumList = res[0].data.data;
      this.SoundList(res[0].data.data);
      console.log(res[0].data.data);
      // console.log("这里是路由更新前", this.albumList, res[0].data.data);

      this.albumBrief = res[1].data.data.albumBriefDetailInfos;

      this.classicAlbums = res[2].data.data;

      this.buyAlbums = res[3].data.data;

      this.showLoading = false;
      this.tomatoesShow = false;
    });
    next();
    this.changeRouterState();
  },
  // },
  data: function () {
    return {
      albumContent: "",
      //作者信息
      albumAuthor: "",
      albumList: "",
      openView: false,
      albumBrief: null,
      AlbumsByPage: null,
      classicAlbums: null,
      buyAlbums: null,
      killHeight: true,
      showLoading: true,
      toggleAlbum: 1,
      zbArr: JSON.parse(localStorage.getItem("zbArr")) || [],
    };
  },

  methods: {
    chuli: function (res) {
      if (res?.startsWith("http")) {
        return res;
      } else if (res?.startsWith("http") == false) {
        return `https://imagev2.xmcdn.com${
          res[0] === "/" ? res : "/" + res
        }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
      } else {
        return "1.png";
      }
    },
    // chuli: function (res) {
    //   if (res?.startsWith("http")) {
    //     return res;
    //   } else if (res?.startsWith("http") === false) {
    //     return `https://imagev2.xmcdn.com${
    //       res[0] === "/" ? res : "/" + res
    //     }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
    //   } else {
    //     return "1.png";
    //   }
    // },
    clickToggleAlbum: function () {
      this.toggleAlbum += 1;
      console.log(this.toggleAlbum);
      this.axios
        .get("https://apis.netstart.cn/ximalaya/album/queryAlbumRelatedRecommendAlbums", {
          params: {
            albumId: this.$route.params.id,
            page: this.toggleAlbum,
            pageSize: 6,
          },
        })
        .then((res) => {
          this.albumBrief = res.data.data.albumBriefDetailInfos;
        });
    },
    zb(zbId) {
      if (!this.zbArr.length) {
        return true;
      } else {
        if (this.zbArr.includes(zbId)) {
          return false;
        } else {
          return true;
        }
      }
    },
    ...mapMutations(["changeRouterState", "SoundList", "changeMusic"]),
  },
};
</script>
<style>
