          }
        }
      }
    }
  }
  .albumPresent {
    margin: 10rem 15rem;
    overflow: hidden;
    position: relative;
    transition: all 0.3s;

    &.killHeight {
      height: 230rem !important;
    }
    &.smallHeight {
      height: 60rem !important;
    }
    &.openAlbum {
      overflow: inherit;
      margin-bottom: 65rem !important;
      .open {
        background: transparent;
        z-index: -1;
        svg {
          display: none;
        }
        .btn {
          position: relative;
          bottom: -65rem;
        }
      }
    }
    .content {
      font-size: 12rem !important;
    }
    .open {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-image: linear-gradient(transparent, 10%, #fff, #fff);
      .svgBox {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          width: 35.42rem;
          height: 24rem;
          margin-bottom: 25rem;
        }
      }
      @keyframes btnMove {
        0% {
          transform: scale(0.95);
        }
        50% {
          transform: scale(1.05);
        }
        100% {
          transform: scale(0.95);
        }
      }
      .btn {
        margin-bottom: 10rem;
        color: #fff;
        animation: btnMove 0.8s linear 0s infinite alternate;
        box-shadow: 0 0 5px 0 rgb(0 0 0 / 20%);
      }
    }
  }
}

