<script>
import { searchEnd } from "../../apis";
import FooterContent from "../../components/footerContent.vue";
export default {
  components: {
    FooterContent,
  },
  data() {
    return {
      searchZhubo: null,
      searchAlbum: null,
      searchVoice: null,
      showLoading: true,
      dataFailed: false,
      value: this.$route.params.id,
    };
  },
  methods: {
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
    songTimes(time) {
      let min = Math.floor(time / 60);
      let sed = Math.floor(time % 60);
      return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
    },
  },
  beforeRouteUpdate(to, from, next) {
    console.log("路由更新");
    setTimeout(() => {
      this.$router.go(0);
    });
    next();
  },
  created() {
    searchEnd({ kw: this.$route.params.id, core: "all", page: 1 })
      .then((res) => {
        console.log(res);
        this.searchZhubo = res.data.data.userViews.users.splice(0, 5);
        this.searchAlbum = res.data.data.albumViews.albums.splice(0, 10);
        this.searchVoice = res.data.data.trackViews.tracks.splice(0, 10);
        this.showLoading = false;
      })
      .catch(() => {
        this.dataFailed = true;
      });
  },
  // watch: {
  //   value: function () {
  //     console.log(123);
  //     setTimeout(() => {
  //       this.$router.go(0);
  //     }, 500);
  //   },
  // },
};
</script>

