<script>
import Swiper from "swiper";
import "swiper/css/swiper.min.css";
import SwiperDemo from "@/components/SwiperCes";
export default {
  components: {
    SwiperDemo,
  },
  mounted() {
    new Swiper(".swiper-container", {
      slidesPerView: 3,
      spaceBetween: 30,
      centeredSlides: true,
      loop: true,
      //如果需要分页器
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
    });
  },
  data: function () {
    return {
      tomatoes: null,
      swiperImages: null,
      moduleRankDatas: null,
      tomatoesShow: true,
    };
  },
  created() {
    this.$emit("change-footer", false);
    this.axios
      .get("https://apis.netstart.cn/ximalaya/index/queryIndexTabContent")
      .then((res) => {
        // console.log(res.data.data.moduleContent.focusImages);
        this.tomatoesShow = false;
        this.tomatoes = res.data.data.moduleContent.tomatoes;
        this.swiperImages = res.data.data.moduleContent.focusImages;
        this.moduleRankDatas = res.data.data.moduleContent.moduleRankDatas;
        this.$emit("change-footer", true);
      });
  },
};
</script>

