<template>
  <section class="voice">
    <div class="loading" v-show="showLoading === true">
      <img src="../../assets/loading.gif" alt="" />
      页面加载中...
    </div>
    <div class="titleAll">
      <nav class="nav" :class="{ hide: !tabhide, show: tabhide }">
        <div
          class="list"
          @click.capture="paixuToggle('paixu')"
          :class="{ active: paixuiValue != '排序方式' }"
        >
          {{ paixuiValue }}
          <!-- 下 -->
          <svg
            v-if="!paixu"
            t="1665642873357"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="3234"
            width="48"
            height="48"
          >
            <path
              d="M500.8 604.779L267.307 371.392l-45.227 45.27 278.741 278.613L779.307 416.66l-45.248-45.248z"
              p-id="3235"
              :fill="`${paixuiValue != '排序方式' ? '#fe7959' : '#a3a3ac'}`"
            ></path>
          </svg>
          <!-- 上 -->
          <svg
            v-else
            t="1665643038688"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="4972"
            width="48"
            height="48"
          >
            <path
              d="M500.8 461.909333L267.306667 695.296l-45.226667-45.269333 278.741333-278.613334L779.306667 650.026667l-45.248 45.226666z"
              p-id="4973"
              fill="#fe7959"
            ></path>
          </svg>
        </div>
        |
        <div
          class="list"
          @click.capture="paixuToggle('fufei')"
          :class="{ active: fufeiValue != '是否付费' }"
        >
          {{ fufeiValue }}
          <!-- 下 -->
          <svg
            v-if="!fufei"
            t="1665642873357"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="3234"
            width="48"
            height="48"
          >
            <path
              d="M500.8 604.779L267.307 371.392l-45.227 45.27 278.741 278.613L779.307 416.66l-45.248-45.248z"
              p-id="3235"
              :fill="`${fufeiValue != '是否付费' ? '#fe7959' : '#a3a3ac'}`"
            ></path>
          </svg>
          <!-- 上 -->
          <svg
            v-else
            t="1665643038688"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="4972"
            width="48"
            height="48"
          >
            <path
              d="M500.8 461.909333L267.306667 695.296l-45.226667-45.269333 278.741333-278.613334L779.306667 650.026667l-45.248 45.226666z"
              p-id="4973"
              fill="#fe7959"
            ></path>
          </svg>
        </div>
      </nav>
      <div class="qita" :class="{ active: paixu }">
        <div
          :class="{ active: paixuiValue == '相关度' }"
          @click="paixuiValue != '相关度' ? changeCondition('relation', '相关度') : false"
        >
          相关度
        </div>
        <div
          :class="{ active: paixuiValue == '最多播放' }"
          @click="paixuiValue != '最多播放' ? changeCondition('play', '最多播放') : false"
        >
          最多播放
        </div>
        <div
          :class="{ active: paixuiValue == '最新上传' }"
          @click="
            paixuiValue != '最新上传' ? changeCondition('recent', '最新上传') : false
          "
        >
          最新上传
        </div>
      </div>
      <div class="qita" :class="{ active: fufei }">
        <div
          :class="{ active: fufeiValue == '全部' }"
          @click="fufeiValue != '全部' ? changepaidFilter(false, '全部') : false"
        >
          全部
        </div>
        <div
          :class="{ active: fufeiValue == '免费' }"
          @click="fufeiValue != '免费' ? changepaidFilter(true, '免费') : false"
        >
          免费
        </div>
        <div
          :class="{ active: fufeiValue == '付费' }"
          @click="fufeiValue != '付费' ? changepaidFilter(true, '付费') : false"
        >
          付费
        </div>
      </div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
    >
      <section class="voiceBox">
        <ul class="voiceList" v-if="searchVoice">
          <li
            class="item"
            v-for="item in searchVoice"
            :key="item.trackInfo.id"
            @click="$router.push({ path: `${item.pageUriInfo.url}` })"
          >
            <div class="cover_path">
              <img :src="item.trackInfo.cover_path" alt="" />
              <div class="mark">
                <svg
                  t="1664887724993"
                  class="icon"
                  viewBox="0 0 1024 1024"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  p-id="3159"
                  width="48"
                  height="48"
                >
                  <path
                    d="M852.727563 392.447107C956.997809 458.473635 956.941389 565.559517 852.727563 631.55032L281.888889 993.019655C177.618644 1059.046186 93.090909 1016.054114 93.090909 897.137364L93.090909 126.860063C93.090909 7.879206 177.675064-35.013033 281.888889 30.977769L852.727563 392.447107 852.727563 392.447107Z"
                    p-id="3160"
                    fill="#ffffff"
                  ></path>
                </svg>
              </div>
            </div>
            <div class="trackInfoCont">
              <div class="title van-multi-ellipsis--l2">{{ item.trackInfo.title }}</div>
              <div class="bottom">
                <div class="nickname">
                  <svg
                    t="1665572759850"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="12591"
                    width="48"
                    height="48"
                  >
                    <path
                      d="M650.3 535.5C721.1 489.9 768 410.4 768 320c0-141.4-114.6-256-256-256S256 178.6 256 320c0 90.5 46.9 170 117.8 215.5C211.9 592.5 96 746.7 96 928v0.3c0 17.5 14.4 31.7 31.9 31.7h769c17.2 0 31-13.9 31.1-31.1v-3.2c-1-180.8-116.5-333.6-277.7-390.2z m-274.1-79.7C340 419.5 320 371.3 320 320s20-99.5 56.2-135.8C412.5 148 460.7 128 512 128s99.5 20 135.8 56.2C684 220.5 704 268.7 704 320s-20 99.5-56.2 135.8C611.5 492 563.3 512 512 512s-99.5-20-135.8-56.2zM161.4 896.1c3.2-36.2 12-71.4 26.2-105 17.7-41.9 43.1-79.6 75.5-111.9 32.3-32.3 70-57.7 111.9-75.5 43.4-18.4 89.5-27.7 137-27.7s93.5 9.2 136.7 27.4c41.8 17.6 79.4 42.8 111.6 74.9 32.3 32.1 57.7 69.5 75.5 111.2 14.5 34 23.5 69.7 26.8 106.6H161.4z"
                      p-id="12592"
                      fill="#73737b"
                    ></path></svg
                  ><span>{{ item.trackInfo.nickname }}</span>
                </div>
                <div class="duration">
                  <svg
                    t="1664889165758"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="24215"
                    width="48"
                    height="48"
                  >
                    <path
                      d="M512 1024A512 512 0 1 0 512 0a512 512 0 0 0 0 1024z m0-73.142857A438.857143 438.857143 0 1 1 512 73.142857a438.857143 438.857143 0 0 1 0 877.714286z"
                      fill="#a6a6a6"
                      p-id="24216"
                    ></path>
                    <path
                      d="M512 513.974857h260.096a36.571429 36.571429 0 1 1 0 73.142857h-292.571429a36.425143 36.425143 0 0 1-18.285714-4.827428A36.571429 36.571429 0 0 1 438.857143 548.571429v-292.571429a36.571429 36.571429 0 0 1 73.142857 0v257.974857z"
                      fill="#a6a6a6"
                      p-id="24217"
                    ></path></svg
                  >{{ songTimes(item.trackInfo.duration) }}
                </div>
                <div class="count_play">
                  <svg
                    t="1665023374138"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="2376"
                    width="48"
                    height="48"
                  >
                    <path
                      d="M224 938.713333a53.58 53.58 0 0 1-53.333333-53.433333V138.72a53.333333 53.333333 0 0 1 80.886666-45.666667l618.666667 373.28a53.333333 53.333333 0 0 1 0 91.333334l-618.666667 373.28a53.16 53.16 0 0 1-27.553333 7.766666z m0.046667-810.666666a10.98 10.98 0 0 0-5.333334 1.42 10.466667 10.466667 0 0 0-5.38 9.253333v746.56a10.666667 10.666667 0 0 0 16.18 9.133333l618.666667-373.28a10.666667 10.666667 0 0 0 0-18.266666l-618.666667-373.28a10.386667 10.386667 0 0 0-5.446666-1.586667z"
                      fill="#999999"
                      p-id="2377"
                    ></path>
                  </svg>
                  {{
                    item.trackInfo.count_play > 100000000
                      ? (item.trackInfo.count_play / 100000000).toFixed(2) + "亿"
                      : item.trackInfo.count_play > 10000
                      ? (item.trackInfo.count_play / 10000).toFixed(2) + "万"
                      : item.trackInfo.count_play
                  }}
                </div>
              </div>
            </div>
          </li>
        </ul>
      </section>
    </van-list>
    <FooterContent />
    <div class="mark" v-show="paixu || fufei" @click="paixuToggle()"></div>
  </section>
</template>

<script>
// SearchFilterData
import { SearchFilterData } from "../../apis";
import FooterContent from "../../components/footerContent.vue";
let num = 1;
export default {
  components: {
    FooterContent,
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      searchVoice: [],
      showLoading: true,
      error: false,
      paixu: false,
      paixuiValue: "排序方式",
      fufei: false,
      fufeiValue: "是否付费",
      paidFilter: false,
      fq: false,
      selectAll: true,
      tabhide: true,
      oldScrollTop: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.scrolling);
  },
  beforeRouteLeave(to, from, next) {
    // console.log("离开了专辑");
    document.querySelector("body").style.overflow = "initial";
    next();
  },
  created() {
    // searchEnd({ kw: this.$route.params.id, core: "tarck", page: num }).then((res) => {
    //   console.log(res);
    // });
  },
  methods: {
    sendData: function ({
      kw,
      page,
      category_id,
      condition,
      paidFilter,
      selectAll,
    } = {}) {
      SearchFilterData({
        kw,
        core: "track",
        page,
        category_id,
        condition,
        paidFilter,
        fqCont: selectAll ? this.fq : false,
      }).then((o) => {
        this.searchVoice = o.data.data.trackViews.tracks;
      });
    },
    scrolling() {
      try {
        // console.log(document.body.scrollTop, window.pageYOffset);
        if (window.pageYOffset === 0) {
          // console.log("到头了");
          this.tabhide = true;
        } else {
          this.tabhide = false;
        }
      } catch (error) {
        error;
      }
    },
    songTimes(time) {
      let min = Math.floor(time / 60);
      let sed = Math.floor(time % 60);
      return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
    },
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
    numAdd: function () {
      num++;
    },
    onLoad() {
      // searchEnd({ kw: this.$route.params.id, core: "track", page: num })
      SearchFilterData({
        kw: this.$route.params.id,
        core: "track",
        page: num,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      })
        .then((res) => {
          this.numAdd();
          let arr = res.data.data.trackViews.tracks;
          // console.log(this.searchVoice, arr);
          this.searchVoice = new Set([...this.searchVoice, ...arr]);
          this.loading = false;
          if (this.searchVoice.length <= 40) {
            this.finished = true;
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },
    changeCondition: function (state, name) {
      console.log(state, name);
      this.condition = state;
      this.paixuiValue = name;
      this.sendData({
        kw: this.$route.params.id,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      });
    },
    //是否付费的筛选
    changepaidFilter: function (state, name) {
      this.paidFilter = state;
      this.fufeiValue = name;
      name == "全部"
        ? ((this.selectAll = false), (this.fq = ""))
        : name == "免费"
        ? (this.fq = false)
        : (this.fq = true);
      this.sendData({
        kw: this.$route.params.id,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      });
    },

    //显示菜单与遮罩
    paixuToggle(name) {
      if (name == "paixu") {
        this.paixu = !this.paixu;
        this.fenlei = false;
        this.fufei = false;
        if (this.paixu) {
          document.querySelector("body").style.overflow = "hidden";
        } else {
          document.querySelector("body").style.overflow = "initial";
        }
      } else if (name == "fufei") {
        this.fufei = !this.fufei;
        this.fenlei = false;
        this.paixu = false;
        if (this.fufei) {
          document.querySelector("body").style.overflow = "hidden";
        } else {
          document.querySelector("body").style.overflow = "initial";
        }
      } else {
        this.fufei = false;
        this.fenlei = false;
        this.paixu = false;
        document.querySelector("body").style.overflow = "initial";
      }
    },
  },
  watch: {
    searchVoice() {
      this.showLoading = false;
    },
  },
};
</script>

<style lang="less" scoped>
.titleAll {
}
.mark {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.166);
  z-index: 12;
}
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: -1;
  height: 100%;
  background-color: #fff;
  z-index: 50;
  font-size: 15rem;
}
.titleAll {
  position: relative;
  padding: 15rem 0;
  .nav {
    min-height: 40rem;
    position: fixed;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #fff;
    top: 94rem;
    border-bottom: 1rem solid #00000017;
    width: 100%;
    z-index: 26;
    transition: all 0.3s;
    padding: 0 20rem;
    &.show {
      transform: translateY(0);
    }
    &.hide {
      transform: translateY(-100%);
    }
    .list {
      width: 30%;
      text-align: center;
      display: flex;
      align-items: center;
      font-size: 14rem;
      justify-content: center;
      color: #a3a3ac;
      &.active {
        color: #fe7959;
      }
      svg {
        margin-left: 5rem;
        width: 18rem;
        height: 18rem;
      }
    }
  }
  .qita {
    background-color: #fff;
    position: fixed;
    width: 100%;
    text-align: center;
    transform: translateY(-500rem);
    transition: all 0.3s;
    z-index: 20;
    &.active {
      transform: translateY(19%);
      .active {
        color: #fe7959;
      }
    }
    div {
      padding: 15rem 0;
      font-size: 15rem;
      &:not(:last-child) {
        border-bottom: 1rem solid #cccccc38;
      }
    }
  }
}
.voiceBox {
  padding: 5rem 0rem 5rem 15rem;

  .voiceList {
    .item {
      overflow: hidden;
      padding: 15rem 0;
      display: flex;
      align-items: start;
      &:not(:last-child) {
        border-bottom: 1rem solid rgba(0, 0, 0, 0.04);
      }
      .cover_path {
        width: 50rem;
        height: 50rem;
        flex-shrink: 0;
        position: relative;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
        .mark {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          z-index: 12;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #0000004d;
          border-radius: 50%;
          svg {
            width: 15rem;
            height: 15rem;
          }
        }
      }
      .trackInfoCont {
        padding: 0 10rem;
        .title {
          font-size: 16rem;
          margin-bottom: 10rem;
        }
        .bottom {
          display: flex;
          align-items: center;
          .nickname {
            display: flex;
            align-items: center;
            margin-right: 15rem;
            color: #a3a3ac;
            svg {
              width: 13rem;
              height: 13rem;
              margin-right: 5rem;
            }
            span {
            }
          }
          .duration {
            display: flex;
            align-items: center;
            margin-right: 15rem;
            color: #a3a3ac;
            svg {
              width: 13rem;
              height: 13rem;
              margin-right: 5rem;
              transform: rotateY(180deg);
            }
          }
          .count_play {
            display: flex;
            align-items: center;
            color: #a3a3ac;
            svg {
              width: 13rem;
              height: 13rem;
              margin-right: 5rem;
            }
          }
        }
      }
    }
  }
}
</style>
