<template>
  <ul>
    <li
      class="item"
      v-for="item in itemArr"
      :key="item.id"
      @click="
        $router.replace({ path: `/sound/${item.id}` });
        finterSound(item.id);
        nowPlayMusic(item);
      "
    >
      <!-- emptyPlaylist(soundList.trackDetailInfos ? soundList.trackDetailInfos : []); -->
      <div clsss="item-svg">
        <svg
          t="1664980849933"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="15697"
          width="48"
          height="48"
        >
          <path
            d="M512 960.08A448.18 448.18 0 0 1 337.58 99.15a448.18 448.18 0 0 1 348.84 825.7A445.17 445.17 0 0 1 512 960.08z m0-832c-211.7 0-383.92 172.22-383.92 383.92S300.3 895.92 512 895.92 895.92 723.7 895.92 512 723.7 128.08 512 128.08z m161.72 357.53L472.46 347.66c-21.23-14.56-50.09 0.65-50.09 26.39V650c0 25.74 28.86 40.95 50.09 26.39l201.26-138a32 32 0 0 0 0-52.78z"
            fill="#999999"
            p-id="15698"
          ></path>
        </svg>
      </div>
      <div clsss="item-cont" style="width: 100%">
        <span>{{ item.trackInfo.title }}</span>
      </div>
    </li>
  </ul>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      itemArrState: false,
      itemArr: null,
    };
  },
  computed: {
    ...mapState(["soundList", "audioplay"]),
  },
  // props: ["albumList"],
  created() {
    if (this.soundList) {
      this.itemArr = this.soundList.trackDetailInfos.filter((o) => {
        // console.log(o.id != this.$route.params.id);
        return o.id != this.$route.params.id;
      });
    }
  },

  methods: {
    ...mapMutations(["changeSoundSrc", "SoundList", "emptyPlaylist", "nowPlayMusic"]),
    finterSound(id) {
      this.itemArr = this.soundList.trackDetailInfos.filter((o) => {
        // console.log(o.id != id);
        return o.id != id;
      });
    },
  },
  watch: {
    soundList: function (res) {
      // console.log("Soundlist已经刷新");
      this.itemArr = res.trackDetailInfos.filter((o) => {
        return o.id != this.$route.params.id;
      });
    },
  },
};
</script>
<style lang="less" scoped>
.item {
  padding: 10rem 0;
  display: flex;
  align-items: stretch;
  justify-content: center;
  border-bottom: 1rem solid rgba(153, 153, 153, 0.12);
  svg {
    width: 30rem;
    height: 30rem;
  }
  > .item-cont {
    width: 100%;
    overflow: hidden;
    background-color: #f86442;
  }
  span {
    font-size: 16rem;
    padding: 0 10rem;
    line-height: 26px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}
.open {
  font-size: 15rem;
  font-weight: bold;
  color: #f86442;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 17rem 0;
  svg {
    width: 20rem;
    height: 20rem;
  }
}
</style>
