<template>
  <div class="con" v-if="inform">
    <section
      class="infromCon"
      v-if="
        inform?.anchorInfo?.userInfo?.personalSignature ||
        inform?.profile?.interests.length
      "
    >
      <h3 class="title">TA的信息</h3>
      <van-skeleton title :row="2" :loading="loading">
        <div class="SignatureBox">
          <div>简介:</div>
          <p v-if="inform.anchorInfo.userInfo.personalSignature">
            {{ inform.anchorInfo.userInfo.personalSignature }}
          </p>
        </div>

        <!-- <p>听众等级:{{}}</p> -->
        <ul v-if="inform?.profile?.interests.length" class="informProfile">
          <div class="titles">兴趣:</div>
          <div class="itemBox">
            <li
              class="item"
              v-for="(item, index) in inform.profile.interests"
              :key="index"
            >
              {{ item }}
            </li>
          </div>
        </ul>
      </van-skeleton>
    </section>

    <section v-if="inform?.profile?.communityInfos.length" class="circle">
      <h3 class="title">TA的圈子</h3>
      <ul class="informList">
        <van-skeleton title avatar :row="2" :loading="loading">
          <li class="item" v-for="item in inform?.profile?.communityInfos" :key="item.id">
            <div class="imgLeft">
              <img :src="chuli(item?.logo)" alt="故事头像" />
            </div>
            <div class="right">
              <h2 class="name van-ellipsis">{{ item.name }}</h2>
              <p class="memberCount van-ellipsis">
                <span>成员</span>{{ item.memberCount }} <span>帖子</span>
                {{ item.articleCount }}
              </p>
              <p class="intro van-ellipsis">介绍: {{ item.intro }}</p>
            </div>
          </li>
        </van-skeleton>
      </ul>
    </section>

    <div
      class="allFalse"
      v-if="
        !inform.anchorInfo.userInfo.personalSignature && !inform.profile.interests.length
      "
    >
      该主播还未填充信息噢,请查看别的把~
    </div>
  </div>
</template>

<script>
export default {
  props: ["inform", "loading"],
  created() {},
  data() {
    return {};
  },
  methods: {
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com${
          res[0] === "/" ? res : "/" + res
        }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
      } else {
        return "1.png";
      }
    },
  },
};
</script>
<style>
.van-skeleton {
  padding: 0;
}
.van-skeleton .van-skeleton__content {
  padding-top: 0;
}
.van-skeleton .van-skeleton__avatar {
  width: 85rem;
  height: 85rem;
  border-radius: 5rem;
}
</style>
<style lang="less" scoped>
.con {
  padding: 15rem;
  .allFalse {
    min-height: 150rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15rem;
  }
  .infromCon {
    p {
      font-size: 14rem;
      margin-bottom: 10rem;
      color: #999;
    }
    .informProfile {
      display: flex;
      align-items: stretch;
      span {
        font-size: 14rem;
        margin-bottom: 10rem;
        color: #999;
        margin-right: 15rem;
      }
      div.titles {
        margin-right: 5rem;
        margin-bottom: 10rem;
        font-size: 14rem;
        margin-bottom: 10rem;
        color: #999;
        flex-shrink: 0;
      }
      .item {
        padding: 2px 8px;
        margin-bottom: 10rem;
        font-size: 13rem;
        border-radius: 4rem;
        margin-right: 10rem;
        flex-wrap: wrap;
        color: #666;
        background: #f3f4f5;
      }
      .itemBox {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
      }
    }
    .SignatureBox {
      display: flex;
      div {
        margin-bottom: 10rem;
        font-size: 14rem;
        border-radius: 4rem;
        margin-right: 5rem;
        flex-wrap: wrap;
        color: #999;
        flex-shrink: 0;
      }
    }
  }
}
.title {
  font-size: 18rem;
  // font-weight: bold;
  margin-bottom: 10rem;
}
.circle {
  margin: 23rem 0 10rem;

  .informList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    .item {
      display: flex;
      align-items: center;
    }
    .imgLeft {
      width: 65rem;
      height: 65rem;
      img {
        width: 100%;
        height: 100%;
        border-radius: 4rem;
      }
    }
    .right {
      width: calc(100% - 90rem);
      vertical-align: top;
      margin-left: 10px;
      padding-right: 15px;
      height: 65rem;
      .name {
        font-size: 16rem;
        color: #333;
        margin-bottom: 5rem;
      }
      .memberCount {
        margin-bottom: 5rem;
        color: #999;
        span {
          margin-right: 10rem;
        }
      }
      .intro {
        font-size: 12rem;
        margin-bottom: 10rem;
        color: #999;
      }
    }
  }
}
</style>
