              height: 70rem;
              border-radius: 5rem;
            }
          }
          .right {
            width: calc(100% - 95rem);
            margin-left: 20rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .Rtitle {
              font-size: 14rem;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
            }
            .salePoint {
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              color: #a6a6a6;
              font-size: 12rem;
