<template>
  <div>
    <div class="loading" v-show="showLoading === true">
      <img src="../assets/loading.gif" alt="" />
      页面加载中...
    </div>
    <div>
      <HeaderOpen v-show="showView" />
    </div>
    <header v-if="inform">
      <div class="zhuboGo" @click="$router.go(-1)">
        <svg
          t="1664936691228"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="27497"
          width="48"
          height="48"
        >
          <path
            d="M641.28 278.613333l-45.226667-45.226666-278.634666 278.762666 278.613333 278.485334 45.248-45.269334-233.365333-233.237333z"
            p-id="27498"
            fill="#000"
          ></path>
        </svg>
      </div>
      <div class="topCont">
        <div class="img">
          <figure class="lazyImg" v-lazy:background-image="'1.png'">
            <img v-lazy="chuli(inform?.anchorInfo?.userInfo?.logo)" />
          </figure>
          <div class="vip">V{{ inform?.anchorInfo?.userInfo.anchorGrade }}</div>
        </div>
        <div class="cont">
          <div class="title">{{ inform.anchorInfo.userInfo.nickname }}</div>
          <div class="intro van-ellipsis">
            简介:
            {{
              inform.anchorInfo.userInfo.personalSignature
                ? inform.anchorInfo.userInfo.personalSignature
                : "暂无"
            }}
          </div>
          <div class="authentication" v-if="inform.anchorInfo.userInfo.ptitle">
            喜马认证: {{ inform.anchorInfo.userInfo.ptitle }}
          </div>
        </div>
      </div>
      <div class="outline">
        <div class="left">
          <span>关注</span>
          {{
            inform.anchorInfo.statCountInfo.followingCount > 100000000
              ? (inform.anchorInfo.statCountInfo.followingCount / 100000000).toFixed(2) +
                "亿"
              : inform.anchorInfo.statCountInfo.followingCount > 10000
              ? (inform.anchorInfo.statCountInfo.followingCount / 10000).toFixed(2) + "万"
              : inform.anchorInfo.statCountInfo.followingCount
          }}
        </div>
        <span class="centent">|</span>
        <div class="right">
          <span>粉丝</span>
          {{
            inform.anchorInfo.statCountInfo.followerCount > 100000000
              ? (inform.anchorInfo.statCountInfo.followerCount / 100000000).toFixed(2) +
                "亿"
              : inform.anchorInfo.statCountInfo.followerCount > 10000
              ? (inform.anchorInfo.statCountInfo.followerCount / 10000).toFixed(2) + "万"
              : inform.anchorInfo.statCountInfo.followerCount
          }}
        </div>
      </div>
      <div class="bottom">
        <span v-if="zb(inform.anchorId)" @click.stop="clickZb(inform.anchorId)"
          >+ 关注</span
        >
        <span v-else @click.stop="clickZb(inform.anchorId)" class="select">
          <svg
            t="1666029361371"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="3479"
            width="48"
            height="48"
          >
            <path
              d="M364.037087 836.187497c-5.176906 0-10.354835-1.954515-14.334473-5.872754l-1.402953-1.381463c-0.103354-0.102331-0.205685-0.204661-0.306992-0.309038L66.585511 539.439956c-7.869225-8.087189-7.693216-21.022803 0.393973-28.892027 8.086166-7.867178 21.021779-7.692193 28.891004 0.393973l268.255627 275.66948 565.584406-571.97086c7.934716-8.023744 20.87033-8.096399 28.893051-0.162706 8.024767 7.933693 8.096399 20.87033 0.162706 28.894074L378.565988 830.122361C374.569978 834.162374 369.304044 836.187497 364.037087 836.187497z"
              p-id="3480"
              fill="#f66847"
            ></path>
          </svg>
          已关注
        </span>
      </div>
    </header>

    <!-- 滑动切换  3个组件-->
    <van-tabs swipeable>
      <van-tab v-for="index in contentName" :key="index" :title="index">
        <div class="content" v-show="index === '主页'">
          <InformationPage :inform="inform" :loading="loading" />
        </div>
        <div class="content" v-show="index === '专辑'"><AlbumPage :album="album" /></div>
        <div class="content" v-show="index === '声音'"><VoicePage :voice="voice" /></div>
      </van-tab>
    </van-tabs>
    <div>
      <Footer v-show="showView" />
    </div>
  </div>
</template>

<script>
import InformationPage from "@/components/informationPage.vue";
import AlbumPage from "@/components/AlbumPage.vue";
import VoicePage from "@/components/voicePage.vue";
import HeaderOpen from "@/components/headerOpen.vue";
import Footer from "@/components/footerContent.vue";
export default {
  components: {
    InformationPage,
    AlbumPage,
    VoicePage,
    Footer,
    HeaderOpen,
  },
  data() {
    return {
      contentName: ["主页", "专辑", "声音"],
      //内容更新完成后头部与尾部显示
      showView: false,
      //主播信息
      inform: null,
      //专辑信息
      album: null,
      //声音信息
      voice: null,
      //订阅信息
      subscr: null,
      loading: true,
      showLoading: true,
      zbArr: JSON.parse(localStorage.getItem("zbArr")) || [],
    };
  },
  created() {
    this.loading = true;
    this.showView = false;
    this.showLoading = true;
    //作者详情
    let first = this.axios.get(
      `https://apis.netstart.cn/ximalaya/anchor/queryAnchorPage/${this.$route.params.id}`,
      {
        params: {
          pageSize: 20,
          tabType: 2,
        },
      }
    );
    //专辑
    let sed = this.axios.get(
      `https://apis.netstart.cn/ximalaya/anchor/queryAnchorAlbumsByPage`,
      {
        params: {
          anchorId: this.$route.params.id,
          page: 1,
          pageSize: 20,
          asc: false,
        },
      }
    );
    let three = this.axios.get(
      `https://apis.netstart.cn/ximalaya/anchor/queryAnchorTracksByPage`,
      {
        params: {
          anchorId: this.$route.params.id,
          page: 1,
          pageSize: 20,
          asc: false,
        },
      }
    );

    Promise.all([first, sed, three]).then((res) => {
      this.showLoading = false;
      this.showView = true;
      this.inform = res[0].data.data;
      this.album = res[1].data.data.albumBriefDetailInfos;
      this.voice = res[2].data.data.trackDetailInfos;
      this.loading = false;
    });
  },
  methods: {
    chuli: function (res) {
      if (res?.startsWith("http")) {
        return res;
      } else if (res?.startsWith("http") == false) {
        return `https://imagev2.xmcdn.com${
          res[0] === "/" ? res : "/" + res
        }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
      } else {
        return "1.png";
      }
    },
    zb(zbId) {
      if (!this.zbArr.length) {
        return true;
      } else {
        if (this.zbArr.includes(zbId)) {
          return false;
        } else {
          return true;
        }
      }
    },
    clickZb(zbId) {
      if (!this.zbArr.length) {
        this.zbArr = [zbId, ...this.zbArr];
        localStorage.setItem("zbArr", JSON.stringify(this.zbArr));
        return true;
      } else {
        if (this.zbArr.includes(zbId)) {
          this.zbArr = this.zbArr.filter((o) => o !== zbId);
          localStorage.setItem("zbArr", JSON.stringify(this.zbArr));
          return false;
        } else {
          this.zbArr = [zbId, ...this.zbArr];
          localStorage.setItem("zbArr", JSON.stringify(this.zbArr));
          return true;
        }
      }
    },
  },
};
</script>

<style>
.van-tabs__wrap {
  border-bottom: 0.5rem solid #cccccc52;
}
.van-tabs .van-tab .van-tab__text {
  font-size: 15rem;
  color: #858585;
  transition: all 0.3s;
}
.van-tabs__wrap {
  border-bottom: calc(1rem * 0.5) solid #eee;
}
.van-tabs .van-tabs__wrap .van-tabs__nav {
  padding: 0 0 15rem 0;
}
.van-tabs .van-tab.van-tab--active .van-tab__text {
  font-size: 18rem;
  color: #000;
}
</style>
<style lang="less" scoped>
