  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 10;
  font-size: 15rem;
}
.lazyImg {
  width: 85rem;
  height: 85rem;
  border-radius: 50%;
}
header {
  .zhuboGo {
    position: absolute;
    top: 65rem;
    left: 10rem;
    z-index: 50;
    svg {
      width: 30rem;
      height: 30rem;
    }
  }
  .topCont {
    width: 92%;
    padding-top: 40rem;
    margin: 0 auto;
    display: flex;
    align-items: stretch;
    justify-content: center;
    .img {
      position: relative;
      width: 85rem;
      height: 85rem;
      flex-shrink: 0;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
      .vip {
        position: absolute;
        right: 0;
        bottom: 0;
        border-radius: 50rem;
        background-color: #4dd4fa;
        color: #fff;
        font-weight: bold;
        font-size: 14rem;
        padding: 2rem 4rem;
      }
    }
    .cont {
      display: flex;
      flex-direction: column;
      justify-content: stretch;
      padding: 0rem 20rem;
      width: calc(100% - 85rem);
      .title {
        font-size: 18rem;
        margin-bottom: 5rem;
      }
      .intro {
        padding: 5rem;
        background: rgba(0, 0, 0, 0.07);
        color: #858585;
        margin-bottom: 5rem;
      }
      .authentication {
        padding: 5rem 0;
        color: #858585;
      }
    }
  }
  .outline {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 16px;
    margin-top: 22px;
    .left {
      font-size: 14rem;
      span {
        font-size: 12rem;

        margin-right: 18rem;
        color: #858585;
      }
    }
    .centent {
      margin: 0 20rem;
      font-size: 15rem;
      font-weight: 100;
      color: #858585;
    }
    .right {
      font-size: 14rem;
      span {
        font-size: 12rem;
        color: #858585;
        margin-right: 20rem;
      }
    }
  }
  .bottom {
    margin: 12px auto 6px;
    margin: 10rem 0;
    display: flex;
    justify-content: center;
    span {
      border-radius: 50rem;
      padding: 5rem 25rem;
      font-size: 15rem;
      color: #fff;
      background-color: #f86442;
      display: flex;
      align-items: center;
      transition: all 0.3s;
      &.select {
        padding: 5rem 15rem;
        background-color: transparent;
        outline: 2rem solid #f86442;
        border-radius: 50rem;
        transition: all 0.3s;
        color: #f86442;
        svg {
          width: 15rem;
          margin-right: 5rem;
          height: 15rem;
        }
      }
    }
  }
}
</style>
