<template>
  <div>
    <div class="bigBox" v-show="!tomatoesShow">
      <div class="homeTop" v-show="!tomatoesShow">
        <div>
          <img src="../assets/indexpict.png" alt="" />
        </div>
      </div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
      @load="onLoad"
      v-if="moreRecommendations"
    >
      <div class="moreRecommendations">
        <div class="moreCenterBox">
          <div class="title">更多推荐</div>
          <div class="verBox">
            <div
              class="verticalData"
              v-for="n in moreRecommendations"
              :key="n?.data?.title"
              @click="
                n?.data?.albumInfo
                  ? $router.push({ path: `/album/${n.data.id}` })
                  : $router.push({ path: `/sound/${n.data.id}` })
              "
            >
              <div class="verticalDataBox">
                <div class="left">
                  <img
                    :class="n?.data?.albumInfo ? 'square' : 'round'"
                    :src="`${
                      n?.data?.albumInfo?.cover
                        ? `${
                            chuli(n?.data?.albumInfo?.cover)
                              ? `${n?.data?.albumInfo?.cover}`
                              : `https://imagev2.xmcdn.com/${n?.data?.albumInfo?.cover}!op_type=3&columns=144&rows=144&magick=webp`
                          }`
                        : n?.data?.trackInfo.cover
                        ? `https://imagev2.xmcdn.com/${n?.data?.trackInfo.cover}!op_type=3&columns=144&rows=144&magick=webp`
                        : '1.png'
                    }`"
                    alt=""
                  />
                  <div class="mark" v-if="n?.data?.trackInfo">
                    <!-- 播放 -->
                    <svg
                      t="1664887724993"
                      class="icon"
                      viewBox="0 0 1024 1024"
                      version="1.1"
                      xmlns="http://www.w3.org/2000/svg"
                      p-id="3159"
                      width="48"
                      height="48"
                    >
                      <path
                        d="M852.727563 392.447107C956.997809 458.473635 956.941389 565.559517 852.727563 631.55032L281.888889 993.019655C177.618644 1059.046186 93.090909 1016.054114 93.090909 897.137364L93.090909 126.860063C93.090909 7.879206 177.675064-35.013033 281.888889 30.977769L852.727563 392.447107 852.727563 392.447107Z"
                        p-id="3160"
                        fill="#ffffff"
                      ></path>
                    </svg>
                    <!-- 暂停 -->
                    <!-- <svg t="1664887803707" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4230" width="48" height="48"><path d="M268.97201558 114.31784297c73.21218086 0 132.56071902 59.34853814 132.56071901 132.560719v530.24287606c0 73.21218086-59.34853814 132.56071902-132.56071901 132.560719s-132.56071902-59.34853814-132.56071901-132.560719V246.87856197c0-73.21218086 59.34853814-132.56071902 132.56071901-132.560719z m486.05596884 0c73.21218086 0 132.56071902 59.34853814 132.56071901 132.560719v530.24287606c0 73.21218086-59.34853814 132.56071902-132.56071901 132.560719s-132.56071902-59.34853814-132.56071901-132.560719V246.87856197c0-73.21218086 59.34853814-132.56071902 132.56071901-132.560719z" p-id="4231" fill="#ffffff"></path></svg> -->
                  </div>
                </div>
                <div class="right">
                  <div>
                    <div class="Rtitle">
                      {{
                        n?.data?.albumInfo
                          ? n?.data?.albumInfo?.title
                          : n.data.trackInfo.title
                      }}
                    </div>
                    <div class="salePoint">
                      {{
                        n?.data?.albumInfo
                          ? n?.data?.albumInfo?.salePoint
                          : n.data.trackInfo.intro
                      }}
                    </div>
                  </div>
                  <div class="statCountInfo">
                    <div class="diso" v-if="n.data.statCountInfo.trackCount">
                      <svg
                        t="1664763290338"
                        class="icon"
                        viewBox="0 0 1024 1024"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        p-id="14843"
                        width="48"
                        height="48"
                      >
                        <path
                          d="M64 640a21.333333 21.333333 0 0 1-21.333333-21.333333v-213.333334a21.333333 21.333333 0 0 1 42.666666 0v213.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM192 810.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-554.666666a21.333333 21.333333 0 0 1 42.666666 0v554.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM320 981.333333a21.333333 21.333333 0 0 1-21.333333-21.333333v-896a21.333333 21.333333 0 0 1 42.666666 0v896a21.333333 21.333333 0 0 1-21.333333 21.333333zM448 938.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-810.666666a21.333333 21.333333 0 0 1 42.666666 0v810.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM576 768a21.333333 21.333333 0 0 1-21.333333-21.333333v-469.333334a21.333333 21.333333 0 0 1 42.666666 0v469.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM704 938.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-810.666666a21.333333 21.333333 0 0 1 42.666666 0v810.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM832 768a21.333333 21.333333 0 0 1-21.333333-21.333333v-469.333334a21.333333 21.333333 0 0 1 42.666666 0v469.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM960 640a21.333333 21.333333 0 0 1-21.333333-21.333333v-213.333334a21.333333 21.333333 0 0 1 42.666666 0v213.333334a21.333333 21.333333 0 0 1-21.333333 21.333333z"
                          fill="#73737b"
                          p-id="14844"
                        ></path>
                      </svg>
                      {{ n.data.statCountInfo.trackCount }}
                    </div>
                    <div
                      class="play"
                      :class="n?.data?.trackInfo?.duration ? 'reduceLeft' : ''"
                    >
                      <svg
                        t="1664763361150"
                        class="icon"
                        viewBox="0 0 1024 1024"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        p-id="18242"
                        width="48"
                        height="48"
                      >
                        <path
                          d="M921.6 724.2752a25.6 25.6 0 0 1-25.6-25.6V506.88a368.64 368.64 0 1 0-737.28 0v192a25.6 25.6 0 0 1-51.2 0V506.88a419.84 419.84 0 1 1 839.68 0v192a25.6 25.6 0 0 1-25.6 25.3952z"
                          fill="#73737b"
                          p-id="18243"
                        ></path>
                        <path
                          d="M355.1744 796.7232a67.072 67.072 0 0 1-134.144 0V562.688a67.072 67.072 0 0 1 134.144 0z"
                          fill="#73737b"
                          p-id="18244"
                        ></path>
                        <path
                          d="M194.6624 577.4848m25.6 0l0 0q25.6 0 25.6 25.6l0 155.2896q0 25.6-25.6 25.6l0 0q-25.6 0-25.6-25.6l0-155.2896q0-25.6 25.6-25.6Z"
                          fill="#73737b"
                          p-id="18245"
                        ></path>
                        <path
                          d="M699.7504 558.4384a67.072 67.072 0 0 1 134.144 0v234.0352a67.072 67.072 0 0 1-134.144 0z"
                          fill="#73737b"
                          p-id="18246"
                        ></path>
                        <path
                          d="M860.2624 779.7248m-25.6 0l0 0q-25.6 0-25.6-25.6l0-155.2896q0-25.6 25.6-25.6l0 0q25.6 0 25.6 25.6l0 155.2896q0 25.6-25.6 25.6Z"
                          fill="#73737b"
                          p-id="18247"
                        ></path>
                      </svg>
                      {{
                        n.data.statCountInfo.playCount > 100000000
                          ? (n.data.statCountInfo.playCount / 100000000).toFixed(2) + "亿"
                          : n.data.statCountInfo.playCount > 10000
                          ? (n.data.statCountInfo.playCount / 10000).toFixed(2) + "万"
                          : n.data.statCountInfo.playCount
                      }}
                    </div>
                    <div class="times" v-if="n?.data?.trackInfo?.duration">
                      <svg
                        t="1664889165758"
                        class="icon"
                        viewBox="0 0 1024 1024"
                        version="1.1"
                        xmlns="http://www.w3.org/2000/svg"
                        p-id="24215"
                        width="48"
                        height="48"
                      >
                        <path
                          d="M512 1024A512 512 0 1 0 512 0a512 512 0 0 0 0 1024z m0-73.142857A438.857143 438.857143 0 1 1 512 73.142857a438.857143 438.857143 0 0 1 0 877.714286z"
                          fill="#a6a6a6"
                          p-id="24216"
                        ></path>
                        <path
                          d="M512 513.974857h260.096a36.571429 36.571429 0 1 1 0 73.142857h-292.571429a36.425143 36.425143 0 0 1-18.285714-4.827428A36.571429 36.571429 0 0 1 438.857143 548.571429v-292.571429a36.571429 36.571429 0 0 1 73.142857 0v257.974857z"
                          fill="#a6a6a6"
                          p-id="24217"
                        ></path>
                      </svg>
                      <span>{{ n?.data?.trackInfo?.duration }}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState(["routeName"]),
  },
  methods: {
    onLoad() {
      this.axios
        .get("https://apis.netstart.cn/ximalaya/index/queryCategoryFeed", {
          params: {
            moduleKey: this.$route.query.name,
            times: Date.now(),
          },
        })
        .then((o) => {
          let arr = o.data.data.materials;
          this.moreRecommendations = new Set([...this.moreRecommendations, ...arr]);
          this.loading = false;
          if (this.moreRecommendations.length >= 40) {
            this.finished = true;
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },
    threeArr(obj, name) {
      let arr = [];
      if (obj.albumBriefDetailInfos) {
        if (obj.albumBriefDetailInfos.length && name === "dorizontalData") {
          arr = [...obj.albumBriefDetailInfos];
          arr = arr.splice(0, 3);
          // console.log("ok");
          return arr;
        } else if (obj.albumBriefDetailInfos.length > 3 && name === "verticalData") {
          arr = [...obj.albumBriefDetailInfos];
          arr = arr.splice(3, obj.albumBriefDetailInfos.length);
          // console.log("no ok");
          return arr;
        }
      }
    },
    chuli: function (res) {
      // console.log(res);
      let ok = res.startsWith("http");
      return ok;
    },
  },

  data: function () {
    return {
      tomatoes: null,
      swiperImages: null,
      moduleRankDatas: null,
      tomatoesShow: true,
      dorizontalData: null,
      verticalData: null,
      moreRecommendations: null,
      loading: false,
      finished: false,
      error: false,
      offset: 1,
    };
  },
  created() {
    this.$emit("change-footer", false);
    (this.tomatoesShow = true),
      this.axios
        .get("https://apis.netstart.cn/ximalaya/index/queryIndexCategoryTabContent", {
          params: {
            moduleKey: this.$route.query.name,
          },
        })
        .then((res) => {
          this.tomatoesShow = false;
          this.tomatoes = res.data.data.moduleContent.tomatoes;
          this.swiperImages = res.data.data.moduleContent.focusImages;
          this.moduleRankDatas = res.data.data.moduleContent.moduleRankDatas;
          this.$emit("change-footer", true);
        });
    this.axios
      .get("https://apis.netstart.cn/ximalaya/index/queryCategoryFeed", {
        params: {
          moduleKey: this.$route.query.name,
          // time:Date.now()
        },
      })
      .then((res) => {
        this.moreRecommendations = res.data.data.materials;
      });
  },

  watch: {
    routeName: function (res) {
      this.$emit("change-footer", false);
      (this.tomatoesShow = true),
        this.axios
          .get("https://apis.netstart.cn/ximalaya/index/queryIndexCategoryTabContent", {
            params: {
              moduleKey: res,
            },
          })
          .then((ress) => {
            this.tomatoesShow = false;
            this.tomatoes = ress.data.data.moduleContent.tomatoes;
            this.swiperImages = ress.data.data.moduleContent.focusImages;
            this.moduleRankDatas = ress.data.data.moduleContent.moduleRankDatas;
            this.$emit("change-footer", true);
          });
      this.axios
        .get("https://apis.netstart.cn/ximalaya/index/queryCategoryFeed", {
          params: {
            moduleKey: this.$route.query.name,
          },
        })
        .then((resss) => {
          // console.log(res, resss.data.data.materials);
          this.moreRecommendations = resss.data.data.materials;
        });
      //   next();
    },
  },
};
</script>

<style lang="less" scoped>
.moreRecommendations {
  .moreCenterBox {
    > .title {
      margin: 15rem 0 0 0;
      padding: 10rem;
      font-size: 18rem;
      font-weight: bold;
      color: #40404c;
    }
    .verBox {
      .verticalData {
        .verticalDataBox {
          padding: 15rem 10rem;
          display: flex;
          &:not(:last-child) {
            border-bottom: 1rem solid #cccccc4a;
          }
          .left {
            display: flex;
            align-items: center;
            flex-shrink: 0;
            position: relative;
            img {
              width: 100rem;
              height: 100rem;
              &.square {
                border-radius: 10rem;
              }
              &.round {
                width: 86rem;
                height: 86rem;
                border-radius: 50%;
              }
            }
            .mark {
              position: absolute;
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;
              border-radius: 50%;
              display: flex;
              align-items: center;
              justify-content: center;
              background-color: rgba(54, 54, 54, 0.447);
              svg {
                position: absolute;
                z-index: 9;
                height: 20rem;
                width: 20rem;
              }
            }
          }
          .right {
            padding: 5rem 0;
            padding-right: 40rem;
            width: calc(100% - 125rem);
            margin-left: 20rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .Rtitle {
              font-size: 16rem;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
            .salePoint {
              color: #a6a6a6;
              font-size: 12rem;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              margin: 5rem 0;
            }
            .statCountInfo {
              display: flex;
              align-items: center;
              font-size: 14rem;
              .diso {
                font-size: 14rem;
                display: flex;
                align-items: center;
                color: #a6a6a6;

                svg {
                  width: 15rem;
                  height: 15rem;
                  margin-right: 5rem;
                }
              }
              .play {
                font-size: 14rem;
                display: flex;
                align-items: center;
                margin-left: 20rem;
                color: #a6a6a6;
                &.reduceLeft {
                  margin-left: 0;
                }
                svg {
                  width: 15rem;
                  height: 15rem;
                  margin-right: 5rem;
                }
              }
              .times {
                display: flex;
                align-items: center;
                margin-left: 20rem;
                font-size: 14rem;
                span {
                  color: #a6a6a6;
                }
                svg {
                  width: 15rem;
                  height: 15rem;
                  margin-right: 5rem;
                  transform: rotateY("180deg");
                }
              }
            }
          }
        }
      }
    }
  }
}
.bigBox {
  .homeTop {
    .homeTopList {
      display: flex;
      justify-content: center;
      .homeTopListItem {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin: 20rem 0;
        width: 16%;

        div {
          margin-top: 5rem;
          font-size: 13rem;
          color: #40404c;
        }
      }
    }
    img {
      margin: 10px 0 8px 0;
    }
  }
}
</style>
