<template>
  <div>
    <div class="swiper mySwiper" v-if="swiperImages?.length > 1">
      <div class="swiper-wrapper">
        <div
          class="swiper-slide"
          v-for="img in swiperImages"
          :key="img.adFocusPictureDetail?.adId"
          :style="`backgroundImage:url(${img?.adFocusPictureDetail?.cover})`"
        ></div>
      </div>
    </div>
    <div v-else class="elseTop" :class="{ else: swiperImages?.length == 0 }">
      <img
        v-for="img in swiperImages"
        :key="img.adFocusPictureDetail?.adId"
        :src="img?.adFocusPictureDetail?.cover"
        alt=""
      />
    </div>
  </div>
</template>

<script>
import Swiper from "swiper";
import "swiper/css/swiper.min.css";
export default {
  props: {
    swiperImages: Array,
  },
  mounted() {
    // document.querySelector("body").style.overflowX = "hidden";
    new Swiper(".mySwiper", {
      slidesPerView: 1,
      spaceBetween: 30,
      loop: true,
      autoplay: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
  },
  updated() {
    new Swiper(".mySwiper", {
      slidesPerView: 1,
      spaceBetween: 30,
      loop: true,
      autoplay: true,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
  },
};
</script>

<style lang="less" scoped>
.elseTop {
  width: 100%;
  height: 150rem;
  padding: 5rem 10rem;
  &.else {
    height: 0;
  }
  img {
    width: 100%;
    border-radius: 10rem;
  }
}
.swiper {
  width: 100%;
  height: 150rem;
  padding: 5rem 10rem;
  overflow-x: hidden;
}
// .swiper-slide.swiper-slide-duplicate.swiper-slide-active {
//   transform: scale(0.8);
// }
.swiper-slide {
  border-radius: 10rem;
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  background-position: center;
  background-size: 100% 100%;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}
</style>
