<script>
import { searchEnd, SearchTab, SearchFilterData } from "../../apis";
import FooterContent from "../../components/footerContent.vue";
let num = 1;

export default {
  components: {
    FooterContent,
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      searchAlbum: [],
      showLoading: true,
      error: false,
      searchTab: null,
      categoryId: -1,
      fenlei: false,
      fenleiValue: "分类",
      paixu: false,
      paixuiValue: "排序方式",
      fufei: false,
      fufeiValue: "是否付费",
      condition: "relation",
      paidFilter: false,
      fq: false,
      selectAll: true,
      tabhide: true,
      oldScrollTop: 0,
    };
  },
  watch: {
    searchAlbum() {
      this.showLoading = false;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrolling);
  },
  methods: {
    chuli: function (res) {
      if (res?.startsWith("http")) {
        return res;
      } else if (res?.startsWith("http") == false) {
        return `https://imagev2.xmcdn.com${
          res[0] === "/" ? res : "/" + res
        }!op_type=5&device_type=ios&name=web_meduim&upload_type=cover&magick=webp`;
      } else {
        return "1.png";
      }
    },
    scrolling() {
      try {
        // console.log(document.body.scrollTop, window.pageYOffset);
        if (window.pageYOffset === 0) {
          // console.log("到头了");
          this.tabhide = true;
        } else {
          this.tabhide = false;
        }
      } catch (error) {
        error;
      }
    },
    numAdd: function () {
      num++;
    },
    sendData: function ({
      kw,
      page,
      category_id,
      condition,
      paidFilter,
      selectAll,
    } = {}) {
      SearchFilterData({
        kw,
        core: "album",
        page,
        category_id,
        condition,
        paidFilter,
        fqCont: selectAll ? this.fq : false,
      }).then((o) => {
        this.searchAlbum = o.data.data.albumViews.albums;
      });
    },
    onLoad() {
      // searchEnd({ kw: this.$route.params.id, core: "album", page: num })
      SearchFilterData({
        kw: this.$route.params.id,
        core: "album",
        page: num,
        category_id: this.category_id,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      })
        .then((res) => {
          this.numAdd();
          let arr = res.data.data.albumViews.albums;
          // console.log(this.searchAlbum, arr);
          this.searchAlbum = new Set([...this.searchAlbum, ...arr]);
          this.loading = false;
          if (this.searchAlbum.length <= 40) {
            this.finished = true;
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },
    //分类的筛选
    changeCategory: function (id, name) {
      if ((this.categoryId = id)) {
        this.categoryId = id;
        this.fenleiValue = name;
      }
      this.sendData({
        kw: this.$route.params.id,
        category_id: this.categoryId,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      });
    },
    //排序的筛选
    changeCondition: function (state, name) {
      console.log(state, name);
      this.condition = state;
      this.paixuiValue = name;
      this.sendData({
        kw: this.$route.params.id,
        category_id: this.categoryId,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      });
    },
    //是否付费的筛选
    changepaidFilter: function (state, name) {
      this.paidFilter = state;
      this.fufeiValue = name;
      name == "全部"
        ? ((this.selectAll = false), (this.fq = ""))
        : name == "免费"
        ? (this.fq = false)
        : (this.fq = true);
      this.sendData({
        kw: this.$route.params.id,
        category_id: this.categoryId,
        condition: this.condition,
        paidFilter: this.paidFilter,
        selectAll: this.selectAll,
      });
    },
    //显示菜单与遮罩
    paixuToggle(name) {
      if (name == "fenlei") {
        this.fenlei = !this.fenlei;
        this.paixu = false;
        this.fufei = false;
        if (this.fenlei) {
          document.querySelector("body").style.overflow = "hidden";
        } else {
          document.querySelector("body").style.overflow = "initial";
        }
      } else if (name == "paixu") {
        this.paixu = !this.paixu;
        this.fenlei = false;
        this.fufei = false;
        if (this.paixu) {
          document.querySelector("body").style.overflow = "hidden";
        } else {
          document.querySelector("body").style.overflow = "initial";
        }
      } else if (name == "fufei") {
        this.fufei = !this.fufei;
        this.fenlei = false;
        this.paixu = false;
        if (this.fufei) {
          document.querySelector("body").style.overflow = "hidden";
        } else {
          document.querySelector("body").style.overflow = "initial";
        }
      } else {
        this.fufei = false;
        this.fenlei = false;
        this.paixu = false;
        document.querySelector("body").style.overflow = "initial";
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    // console.log("离开了专辑");
    document.querySelector("body").style.overflow = "initial";
    next();
  },
  created() {
    searchEnd({ kw: this.$route.params.id, core: "album", page: 1 }).then((res) => {
      this.searchAlbum = res.data.data.albumViews.albums;
    });
    SearchTab({ kw: this.$route.params.id }).then((res) => {
      // console.log(res.data.data.categories);
      this.searchTab = res.data.data.categories;
    });
  },
};
</script>

<style lang="less" scoped>
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
