    position: fixed;
    width: 100%;
    text-align: center;
    transform: translateY(-500rem);
    transition: all 0.3s;
    z-index: 20;
    &.active {
      transform: translateY(19%);
      .active {
        color: #fe7959;
      }
    }
    div {
      padding: 15rem 0;
      font-size: 15rem;
      &:not(:last-child) {
        border-bottom: 1rem solid #cccccc38;
      }
    }
  }
}
.albumBox {
  padding: 5rem 0rem 5rem 15rem;
  .titleAll {
    padding-right: 15rem;
  }
  .albumList {
    .item {
      padding: 15rem 0;
      display: flex;
      align-items: stretch;
      overflow: hidden;

      &:not(:last-child) {
        border-bottom: 1rem solid rgba(0, 0, 0, 0.04);
      }
      .leftImg {
        flex-shrink: 0;
        width: 70rem;
        height: 70rem;

        img {
          border-radius: 5rem;
          width: 100%;
          height: 100%;
        }
      }
      .albumInfoCont {
        margin: 0 16rem;
        width: calc(100% - 100rem);
        .title {
          font-size: 16rem;
          margin-bottom: 10rem;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .intro {
          font-size: 13rem;
          line-height: 20rem;
          margin-bottom: 10rem;
          color: #a3a3ac;
          // width: calc(100% - 20rem);
          .van-multi-ellipsis--l3 {
            display: -webkit-box;
            overflow: hidden;
