<template>
  <div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :error.sync="error"
      error-text="请求失败，点击重新加载"
    >
      <section class="voiceBox">
        <ul class="voiceList">
          <li
            class="item"
            v-for="(item, index) in toggleState ? searchVoice : voice"
            :key="index"
            @click="$router.replace({ path: `${item.pageUriInfo.url}` })"
          >
            <!--  -->
            <div class="cover_path">
              <img :src="chuli(item.trackInfo.cover)" alt="" />
              <div class="mark">
                <svg
                  t="1664887724993"
                  class="icon"
                  viewBox="0 0 1024 1024"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  p-id="3159"
                  width="48"
                  height="48"
                >
                  <path
                    d="M852.727563 392.447107C956.997809 458.473635 956.941389 565.559517 852.727563 631.55032L281.888889 993.019655C177.618644 1059.046186 93.090909 1016.054114 93.090909 897.137364L93.090909 126.860063C93.090909 7.879206 177.675064-35.013033 281.888889 30.977769L852.727563 392.447107 852.727563 392.447107Z"
                    p-id="3160"
                    fill="#ffffff"
                  ></path>
                </svg>
              </div>
            </div>
            <div class="trackInfoCont">
              <div class="title van-ellipsis">{{ item.trackInfo.title }}</div>
              <div class="bottom">
                <div class="count_play">
                  <svg
                    t="1665023374138"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="2376"
                    width="48"
                    height="48"
                  >
                    <path
                      d="M224 938.713333a53.58 53.58 0 0 1-53.333333-53.433333V138.72a53.333333 53.333333 0 0 1 80.886666-45.666667l618.666667 373.28a53.333333 53.333333 0 0 1 0 91.333334l-618.666667 373.28a53.16 53.16 0 0 1-27.553333 7.766666z m0.046667-810.666666a10.98 10.98 0 0 0-5.333334 1.42 10.466667 10.466667 0 0 0-5.38 9.253333v746.56a10.666667 10.666667 0 0 0 16.18 9.133333l618.666667-373.28a10.666667 10.666667 0 0 0 0-18.266666l-618.666667-373.28a10.386667 10.386667 0 0 0-5.446666-1.586667z"
                      fill="#999999"
                      p-id="2377"
                    ></path>
                  </svg>
                  {{
                    item.statCountInfo.playCount > 100000000
                      ? (item.statCountInfo.playCount / 100000000).toFixed(2) + "亿"
                      : item.statCountInfo.playCount > 10000
                      ? (item.statCountInfo.playCount / 10000).toFixed(2) + "万"
                      : item.statCountInfo.playCount
                  }}
                </div>
                <div class="count_play pinglun">
                  <svg
                    t="1665751230339"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="3421"
                    width="48"
                    height="48"
                  >
                    <path
                      d="M224 768A138.666667 138.666667 0 0 1 85.333333 629.333333v-362.666666A138.666667 138.666667 0 0 1 224 128h576A138.666667 138.666667 0 0 1 938.666667 266.666667v362.666666A138.666667 138.666667 0 0 1 800 768h-244.821333L341.333333 928a53.333333 53.333333 0 0 1-85.290666-42.666667V768h-32z m309.930667-64h266.069333a74.666667 74.666667 0 0 0 74.666667-74.666667v-362.666666a74.666667 74.666667 0 0 0-74.666667-74.666667H224A74.666667 74.666667 0 0 0 149.333333 266.666667v362.666666c0 41.216 33.450667 74.666667 74.666667 74.666667h95.957333v160l213.973334-160z"
                      p-id="3422"
                      fill="#999999"
                    ></path>
                  </svg>
                  {{
                    item.statCountInfo.commentCount > 100000000
                      ? (item.statCountInfo.commentCount / 100000000).toFixed(2) + "亿"
                      : item.statCountInfo.commentCount > 10000
                      ? (item.statCountInfo.commentCount / 10000).toFixed(2) + "万"
                      : item.statCountInfo.commentCount
                  }}
                </div>
                <div class="duration">
                  <svg
                    t="1664889165758"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="24215"
                    width="48"
                    height="48"
                  >
                    <path
                      d="M512 1024A512 512 0 1 0 512 0a512 512 0 0 0 0 1024z m0-73.142857A438.857143 438.857143 0 1 1 512 73.142857a438.857143 438.857143 0 0 1 0 877.714286z"
                      fill="#a6a6a6"
                      p-id="24216"
                    ></path>
                    <path
                      d="M512 513.974857h260.096a36.571429 36.571429 0 1 1 0 73.142857h-292.571429a36.425143 36.425143 0 0 1-18.285714-4.827428A36.571429 36.571429 0 0 1 438.857143 548.571429v-292.571429a36.571429 36.571429 0 0 1 73.142857 0v257.974857z"
                      fill="#a6a6a6"
                      p-id="24217"
                    ></path></svg
                  >{{ songTimes(item.trackInfo.duration) }}
                </div>
                <div v-if="item.trackInfo.isFree" class="isFree">
                  <span>试听</span>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </section>
    </van-list>
  </div>
</template>

<script>
let num = 2;
export default {
  props: ["voice"],
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      searchVoice: [],
      toggleState: false,
      error: false,
    };
  },
  methods: {
    chuli: function (res) {
      if (res.startsWith("http")) {
        return res;
      } else if (!res.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
    numAdd: function () {
      num++;
    },
    songTimes(time) {
      let min = Math.floor(time / 60);
      let sed = Math.floor(time % 60);
      return `${min < 10 ? `0${min}` : min}:${sed < 10 ? `0${sed}` : sed}`;
    },
    onLoad() {
      this.axios
        .get(`https://apis.netstart.cn/ximalaya/anchor/queryAnchorTracksByPage`, {
          params: {
            anchorId: this.$route.params.id,
            page: num,
            pageSize: 20,
            asc: false,
          },
        })
        .then((res) => {
          console.log(res);
          this.numAdd();
          let arr = res.data.data.trackDetailInfos;
          if (this.toggleState) {
            console.log(this.searchVoice.length, this.toggleState);
            this.searchVoice = new Set([...this.searchVoice, ...arr]);
            arr = new Set([...this.searchVoice, ...arr]);
          } else {
            console.log(this.toggleState);
            this.toggleState = true;
            this.searchVoice = new Set([...this.voice, ...arr]);
          }
          this.loading = false;
          if (arr.length <= 200) {
            console.log(arr.length);
            this.finished = true;
          } else {
            console.log(arr.length);
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },
  },
};
</script>

<style lang="less" scoped>
.voiceBox {
  padding: 0rem 0rem 5rem 15rem;
  .voiceList {
    .item {
      overflow: hidden;
      padding: 15rem 0;
      display: flex;
      align-items: start;
      &:not(:last-child) {
        border-bottom: 1rem solid rgba(0, 0, 0, 0.04);
      }
      .cover_path {
        width: 44rem;
        height: 44rem;
        flex-shrink: 0;
        position: relative;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
        .mark {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          z-index: 12;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #0000004d;
          border-radius: 50%;
          svg {
            width: 15rem;
            height: 15rem;
          }
        }
      }
      .trackInfoCont {
        width: calc(100% - 15%);
        padding: 0 10rem;
        .title {
          margin-bottom: 5px;
          font-size: 16px;
          line-height: 22px;
          max-height: 44px;
          color: #40404c;
        }
        .bottom {
          font-size: 12px;
          line-height: 1.5;
          color: #999;
          display: flex;
          align-items: center;
          .pinglun {
            margin-right: 15px;
            svg {
              width: 13rem;
              height: 13rem;
              margin-right: 5rem;
              transform: rotateY(180deg);
            }
          }
          .duration {
            display: flex;
            align-items: center;
            margin-right: 15rem;
            color: #a3a3ac;
            svg {
              width: 13rem;
              height: 13rem;
              margin-right: 5rem;
              transform: rotateY(180deg);
            }
          }
          .count_play {
            display: flex;
            margin-right: 15rem;
            align-items: center;
            color: #a3a3ac;
            svg {
              width: 13rem;
              height: 13rem;
              margin-right: 5rem;
            }
          }
          .isFree {
            padding: 2px 5px;
            font-size: 11px;
            border: 0.5px solid #13c85e;
            color: #13c85e;
            line-height: 1.1;
            border-radius: 3px;
          }
        }
      }
    }
  }
}
</style>
