<template>
  <ul class="list" :class="{ oldItem: albumBrief.length < 3 }">
    <li
      class="item"
      v-for="item in albumBrief"
      :key="item.id"
      @click="$router.push({ path: `/album/${item.id}` })"
    >
      <div class="dorBox">
        <div class="dorizontalData">
          <div class="dorImg">
            <img
              :src="`${item.albumInfo.cover ? chuli(item.albumInfo.cover) : '1.png'}`"
              alt=""
            />
            <div class="playVolume">
              <svg
                t="1664809796354"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="23724"
                width="48"
                height="48"
              >
                <path
                  d="M509.866667 32C245.333333 32 32 247.466667 32 512s213.333333 480 477.866667 480c264.533333 0 477.866667-215.466667 477.866666-480S774.4 32 509.866667 32z m0 896C281.6 928 96 742.4 96 512S281.6 96 509.866667 96 923.733333 281.6 923.733333 512s-185.6 416-413.866666 416z"
                  fill="#ffffff"
                  p-id="23725"
                ></path>
                <path
                  d="M433.066667 354.133333c-6.4-4.266667-17.066667 0-17.066667 10.666667V661.333333c0 8.533333 8.533333 14.933333 17.066667 10.666667l234.666666-149.333333c6.4-4.266667 6.4-14.933333 0-19.2l-234.666666-149.333334z"
                  fill="#ffffff"
                  p-id="23726"
                ></path>
              </svg>
              <span>
                {{
                  item.statCountInfo.playCount > 100000000
                    ? (item.statCountInfo.playCount / 100000000).toFixed(2) + "亿"
                    : item.statCountInfo.playCount > 10000
                    ? (item.statCountInfo.playCount / 10000).toFixed(2) + "万"
                    : item.statCountInfo.playCount
                }}
              </span>
            </div>
          </div>
          <div class="title">
            <span>
              {{ item.albumInfo.title }}
            </span>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["albumBrief"],
  data: function () {
    return {};
  },
  methods: {
    chuli: function (res) {
      if (res?.startsWith("http")) {
        return res;
      } else if (res?.startsWith("http") === false) {
        return `https://imagev2.xmcdn.com/${res}!op_type=3&columns=144&rows=144&magick=webp`;
      } else {
        return "1.png";
      }
    },
  },
  // created() {
  //   console.log("albumBrief", this.albumBrief);
  // },
};
</script>

<style lang="less" scoped>
.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  &.oldItem {
    justify-content: start;
    .item {
      margin-right: 15rem;
    }
  }
  .item {
    width: 30%;
    flex-shrink: 0;

    .dorBox {
      display: flex;
      align-items: flex-start;
      margin: 7rem 0;
      // margin-right: 7rem;
      //   padding: 10rem 15rem;
      .dorizontalData {
        width: 33.3%;
        // padding: 5rem;
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .dorImg {
          position: relative;
          width: 100%;
          height: 100%;
          border-radius: 5rem;
          img {
            border-radius: 5rem;
            // height: 110rem;
          }
          .playVolume {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 21rem;
            display: flex;
            border-radius: 0 0 5rem 5rem;
            align-items: center;
            padding: 0 3rem;
            transform: scale(0.99);
            background-image: linear-gradient(0deg, black, transparent);
            svg {
              width: 15rem;
              height: 15rem;
              opacity: 0.8;
              margin-right: 5rem;
            }
            span {
              font-size: 12rem;
              color: #fff;
            }
          }
        }
        .title {
          width: 100%;
          span {
            font-size: 13rem;
            color: #333;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            text-align: left;
            width: 100%;
            margin-top: 5rem;
          }
        }
      }
    }
  }
}
</style>
